import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostNoteRequest, PostNoteResponse } from '../@intermediate/models/Note/post-note.model';
import { INoteService } from '../@intermediate/note.interface';

@Injectable({
  providedIn: 'root'
})
export class NoteService implements INoteService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor';
  }

  public PostNote(request: PostNoteRequest): Observable<PostNoteResponse>
  {
    const apiUrl = this.serviceUrl + '/AdjustDetails';
    return this.http.post<PostNoteResponse>(apiUrl, request);
  }
}
