import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import
{
  GetRedeemablePointsRequest,
  GetRedeemablePointsResponse
} from '../@intermediate/models/get-redeemable-points.model';
import { IPointsService } from '../@intermediate/points.interface';

@Injectable()
export class PointsService implements IPointsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/Transaction/';
  }

  GetRedeemablePoints(request: GetRedeemablePointsRequest): Observable<GetRedeemablePointsResponse>
  {
    const apiUrl = this.serviceUrl + 'RedeemablePoints';
    return this.http.get<GetRedeemablePointsResponse>(apiUrl, { params: request as any });
  }
}
