import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DefaultLayoutModule } from '@tei/theme';
import { PaymentAuthGuard } from './guards/payment-auth.guard';
import { DefaultModule } from './pages/default/default.module';
import { FinalizeModule } from './pages/finalize/finalize.module';
import { InitializeModule } from './pages/initialize/initialize.module';
import { PagesComponent } from './pages/pages.component';
import { ProcessModule } from './pages/process/process.module';
import { PaymentProcessorRoutingModule } from './payment-processor-routing.module';
import { PaymentProcessorComponent } from './payment-processor.component';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [DefaultLayoutModule, DefaultModule, InitializeModule, ProcessModule, FinalizeModule];

const COMPONENTS = [PagesComponent, PaymentProcessorComponent];
const PIPES = [];

@NgModule({
  imports: [PaymentProcessorRoutingModule, ...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: [PaymentAuthGuard,]
})
export class PaymentProcessorModule { }
