import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { firstValueFrom } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { CashReturnFrom, PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';

@Component({
  selector: 'tei-common-payment-processor-cash-return',
  templateUrl: './cash-return.component.html',
  styleUrls: ['./cash-return.component.scss'],
})
export class CashReturnComponent implements OnInit
{
  form = new FormGroup<CashReturnFrom>({
    TotalAmount: new FormControl(this.data.totalAmount, Validators.required),
  });
  currencySymbol: string;
  constructor(
    private DialogRef: MatDialogRef<CashReturnComponent>,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private currencyService: TeiCurrencyTranslateService,
    public manageLoadingService: ManageLoadingService)
  {

  }



  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();

  }

  absAmount(amount: number): number
  {
    return Math.abs(amount);
  }

  close(): void
  {
    this.DialogRef.close();
  }

  async confirmReturn(): Promise<void>
  {
    const checkDetails = this.paymentProcessGenericService.changeCheckDetailsSubject.getValue();
    const paymentRequest = new PaymentRequest();
    paymentRequest.PaymentItemID = checkDetails.CheckUID;
    paymentRequest.PaymentMethodID = this.data.paymentMethod.PaymentMethodID;
    paymentRequest.Amount = Number(this.form.value.TotalAmount);
    paymentRequest.OrderID = checkDetails.OrderSeqID;
    switch (this.data.paymentMethod.PaymentMethodID)
    {
      case TenderTypeID.MemberAccount:
        paymentRequest.AccountID = this.data.paymentMethod.PaidTenderID;
        break;
      case TenderTypeID.CashEqCertificate:
        paymentRequest.CashEqItemUID = this.data.paymentMethod.PaidTenderID;
        break;
      case TenderTypeID.OnEvent:
        paymentRequest.EventUID = this.data.paymentMethod.PaidTenderID;
        break;
        break;
      case TenderTypeID.CreditCard:
        paymentRequest.CCName = this.data.paymentMethod.PaidTenderID;
        paymentRequest.IntegratedCreditCard = this.data.IntegratedCreditCard;
        paymentRequest.IntegratedDebitCard = this.data.IntegratedDebitCard;
        paymentRequest.PaymentUID = this.data.paymentMethod.SourcePaymentUID;
        break;
    }
    this.manageLoadingService.startLoading();
    try
    {
      await firstValueFrom(this.paymentProcessGenericService.ProcessPayment(paymentRequest, this.DialogRef));
    }
    catch (error)
    {

    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

}

