import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PaymentVoidRequest, PaymentVoidResponse } from '../@intermediate/models/payment-void/post-payment-void.model';
import { PostVoidCCPaymentRequest, PostVoidCCPaymentResponse } from '../@intermediate/models/payment-void/post-void-cc-payment.model';
import { IVoidPaymentService } from '../@intermediate/models/payment-void/void-payment.service.interface';

@Injectable({
  providedIn: 'root'
})
export class VoidPaymentService implements IVoidPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/payment/void';
  }

  Post(request: PaymentVoidRequest): Observable<PaymentVoidResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PaymentVoidResponse>(apiUrl, request);
  }

  CreditCard(request: PostVoidCCPaymentRequest): Observable<PostVoidCCPaymentResponse>
  {
    const apiUrl = this.serviceUrl + '/CreditCard';
    return this.http.post<PostVoidCCPaymentResponse>(apiUrl, request);
  }
}
