import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot } from '@angular/router';
import { StorageService } from '@tei/core';
import { Observable } from 'rxjs';
import { REFERENCE_KEY_NAME } from '../@intermediate/models/initialize.model';
import { PaymentProcessorComponent } from '../payment-processor.component';

@Injectable({ providedIn: 'root' })
export class DeactivatePaymentProcessor implements CanDeactivate<PaymentProcessorComponent> {
  constructor(
    private storageService: StorageService,
  )
  {
  }

  canDeactivate(
    component: PaymentProcessorComponent,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot
  ): Observable<boolean> | Promise<boolean> | boolean
  {
    this.storageService.Remove(REFERENCE_KEY_NAME);
    return true;
  }
}
