import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { Account } from '../../@intermediate/models/get-account.model';
import { PaymentsTenderedStatus, Printer, PrinterType, TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentResponse } from '../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails } from '../../@intermediate/models/process.model';
@Component({
  selector: 'tei-common-payment-processor-on-account-pay',
  templateUrl: './on-account-pay.component.html',
  styleUrls: ['./on-account-pay.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(OnAccountPayComponent)]
})
export class OnAccountPayComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  accountList = Array<Account>();
  form: UntypedFormGroup;
  showGratuity = false;
  printerList: Printer[] = [];
  selectedAccount: Account;
  tenderTypeID: TenderTypeID;
  checkDetails: CheckDetails;
  result: PaymentResponse;
  subscription = new Subscription();

  get approved(): boolean
  {
    return this.hasApproveTenderType(this.checkDetails);
  }

  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject(MAT_DIALOG_DATA) public onAccountData: any,
    private DialogRef: MatDialogRef<OnAccountPayComponent>,
    private formBuilder: UntypedFormBuilder,
    public manageLoadingService: ManageLoadingService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    if (!this.onAccountData)
    {
      return;
    }

    this.initData();
    this.handleCheckDetailsChanged();
  }

  handleCheckDetailsChanged(): void
  {
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(checkDetails =>
    {
      if (checkDetails.Remaining === 0 && !this.hasApproveTenderType(checkDetails))
      {
        this.close();
      }
      this.checkDetails = checkDetails;
    }));
  }

  private initData(): void
  {
    this.showGratuity = this.onAccountData.showGratuity;
    this.tenderTypeID = TenderTypeID.MemberAccount;
    this.checkDetails = this.onAccountData.checkDetails;

    if (this.onAccountData.accountList && this.onAccountData.accountList !== undefined && this.onAccountData.accountList.length > 0)
    {
      this.accountList = this.onAccountData.accountList;
    }

    this.printerList = this.onAccountData.printerList;
    const defaultPrinter = this.printerList.filter(printer => printer.Type === PrinterType.Primary);

    const paymentTendered = this.checkDetails?.PaymentsTendered?.find(p => p.TenderTypeID === 5 && p.Status !== PaymentsTenderedStatus.Voided);

    const totalApproved = paymentTendered?.Amount ? paymentTendered?.Amount : this.onAccountData.total;
    this.form = this.formBuilder.group({
      totalApproved: [totalApproved, [Validators.required]],
      remainingTotal: [totalApproved, [Validators.required]],
      gratuityValue: ['0.00'],
      gratuityPercent: ['0.00'],
      paymentTotal: [totalApproved],
      printerID: [defaultPrinter.length > 0 ? defaultPrinter[0].PrinterID : null, [Validators.required]]
    });

    const accountID = paymentTendered?.AccountID;
    if (accountID)
    {
      const defaultAccount = this.accountList.filter(a => a.AccountID === accountID);

      if (defaultAccount.length === 1)
      {
        this.selectedAccount = defaultAccount[0];
      }
    }

    this.form.markAllAsTouched();
  }

  close(): void
  {
    this.DialogRef.close();
  }

  approvedApplyHandler($event): void
  {
    this.selectedAccount = $event?.selectedAccount;
    this.result = $event?.result;
  }

  private hasApproveTenderType(checkDetails: CheckDetails): boolean
  {
    return checkDetails?.PaymentsTendered.some(p => p.TenderTypeID === 5 && p.Status === PaymentsTenderedStatus.Approved);
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
