import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ApplicationIdentifier, RouteChangeResolver } from '@tei/core';
import { PaymentAuthGuard } from './guards/payment-auth.guard';
import { DeactivatePaymentProcessor } from './guards/payment-processor.guard';
import { FinalizeComponent } from './pages/finalize/finalize.component';
import { InitializeComponent } from './pages/initialize/initialize.component';
import { PagesComponent } from './pages/pages.component';
import { ProcessComponent } from './pages/process/process.component';
import { PaymentProcessorComponent } from './payment-processor.component';

const routes: Routes = [{
  path: '',
  component: PaymentProcessorComponent,
  canDeactivate: [DeactivatePaymentProcessor],
  children: [
    {
      path: '',
      component: PagesComponent,
      children: [
        // {
        //   path: '',
        //   component: DefaultComponent,
        // },
        {
          path: 'initialize/:Initiator/:RefID',
          redirectTo: 'initialize/:Initiator/:RefID/',
          data: { appIdentifier: ApplicationIdentifier.PaymentProcess },
          resolve: {
            data: RouteChangeResolver
          },
        },
        {
          canActivate: [PaymentAuthGuard],
          path: 'initialize/:Initiator/:RefID/:SubRefID',
          component: InitializeComponent,
          data: { appIdentifier: ApplicationIdentifier.PaymentProcess },
          resolve: {
            data: RouteChangeResolver
          },
        },
        {
          path: 'process/:PaymentRecordUID',
          component: ProcessComponent,
          data: { appIdentifier: ApplicationIdentifier.PaymentProcess },
          resolve: {
            data: RouteChangeResolver
          },
        },
        {
          path: 'finalize',
          component: FinalizeComponent
        }
      ],
    },
    { path: '**', redirectTo: '' },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PaymentProcessorRoutingModule
{
}
