import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';

@Component({
  selector: 'tei-common-payment-processor-check-dialog',
  templateUrl: './check-dialog.component.html',
  styleUrls: ['./check-dialog.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(CheckDialogComponent)]
})
export class CheckDialogComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  form: UntypedFormGroup;
  subscription: Subscription = new Subscription();
  currencySymbol: string;

  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public manageLoadingService: ManageLoadingService,
    private DialogRef: MatDialogRef<CheckDialogComponent>,
    private currencyService: TeiCurrencyTranslateService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.form = this.formBuilder.group({
      paymentAmount: this.data?.total,
    });
    this.valueConversion();
  }

  get paymentAmountControl()
  {
    return this.form.controls.paymentAmount;
  }

  valueConversion(): void
  {
    const paymentAmount = Number(this.paymentAmountControl.value);
    this.paymentAmountControl.patchValue(paymentAmount.toFixed(2));
    this.paymentAmountControl.setErrors(null);
  }

  onApplyPayment(): void
  {
    const request = new PaymentRequest();
    request.OrderID = this.data?.orderSeqID;
    request.PaymentItemID = this.data?.checkUID;
    request.PaymentMethodID = TenderTypeID.Check;
    request.Amount = this.paymentAmountControl?.value;

    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(request, this.DialogRef).subscribe());
  }

  close(): void
  {
    this.DialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
