import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { OnEventDialogData, PostOnEventForm } from '../../@intermediate/models/on-event-payment.model';
import { TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';




@Component({
  selector: 'tei-common-payment-processor-on-event',
  templateUrl: './on-event.component.html',
  styleUrls: ['./on-event.component.scss']
})
export class OnEventComponent implements OnInit
{
  isMobile = false;
  subscription = new Subscription();
  form: FormGroup<PostOnEventForm>;
  formErrorMassage = '';


  constructor(
    public manageLoadingService: ManageLoadingService,
    private responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<OnEventComponent>,
    @Inject(MAT_DIALOG_DATA) public data: OnEventDialogData,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void
  {
    this.getSmallDeviceSize();
    this.createForm();
  };

  createForm(): void
  {
    this.form = new FormGroup<PostOnEventForm>({
      RemainingTotal: new FormControl<number>(0),
      EventID: new FormControl<string>(''),
      PaymentAmount: new FormControl<string>(this.data.PaymentAmount.toFixed(2), [Validators.required])
    });
  }

  changeAmount(): void
  {
    const amountControl = this.form.get('PaymentAmount');
    amountControl.setErrors({ minValue: false });
    amountControl.patchValue(Number(amountControl.value).toFixed(2));
    if (Number(amountControl.value) < 0.01)
    {
      amountControl.setErrors({ minValue: true });
      this.formErrorMassage = this.translate.instant('PAYMENT_PROCESSOR.ON_EVENT.PAYMENT_AMOUNT_MIN_VALUE');
    } else if (Number(amountControl.value) < this.data.PaymentAmount)
    {
      amountControl.setErrors({ minValue: true });
      this.formErrorMassage = this.translate.instant('PAYMENT_PROCESSOR.ON_EVENT.FULL_PAYMENT_REQUIRED');
    }
  }

  getSmallDeviceSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  onApplyPayment(): void
  {
    const checkDetails = this.paymentProcessGenericService.changeCheckDetailsSubject.getValue();
    const paymentRequest = new PaymentRequest();
    paymentRequest.PaymentItemID = checkDetails.CheckUID;
    paymentRequest.PaymentMethodID = TenderTypeID.OnEvent;
    paymentRequest.Amount = Number(this.form.get('PaymentAmount').value);
    paymentRequest.OrderID = checkDetails.OrderSeqID;
    paymentRequest.CashBack = 0.0;
    paymentRequest.EventUID = this.data.EventUID;
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(paymentRequest, this.dialogRef).subscribe());
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
