import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PaymentFinalizeRequest, PaymentFinalizeResponse } from '../@intermediate/models/payment-finalize.model';
import { IPaymentFinalizeService } from '../@intermediate/payment-finalize.interface';

@Injectable({
  providedIn: 'root'
})
export class PaymentFinalizeService implements IPaymentFinalizeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor';
  }

  public PostFinalize(request: PaymentFinalizeRequest): Observable<PaymentFinalizeResponse>
  {
    const apiUrl = this.serviceUrl + '/Finalize';
    return this.http.post<PaymentFinalizeResponse>(apiUrl, request);
  }
}
