import { SelectionModel } from '@angular/cdk/collections';
import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ResponsiveService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { IPaymentProcessGenericService } from '../../../@intermediate/IPaymentProcessGenericService.interface';
import { PaymentsTendered, TenderTypeID } from '../../../@intermediate/models/payment-process/get-process.model';
import { VoidPaymentDialogRequest, VoidPaymentDialogRequestForm } from '../../../@intermediate/models/post-void-payment.model';
import { CheckDetails } from '../../../@intermediate/models/process.model';

@Component({
  selector: 'tei-common-payment-processor-void-tender-payments',
  templateUrl: './void-tender-payments.component.html',
  styleUrls: ['./void-tender-payments.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(VoidTenderPaymentsComponent)]
})
export class VoidTenderPaymentsComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  @Input() payments = new Array<PaymentsTendered>();
  @Input() integratedCreditPayments = new Array<PaymentsTendered>();
  @Input() selection = new SelectionModel<PaymentsTendered>(true, []);
  @Input() checkDetails: CheckDetails;
  isMobile = false;
  @Output() voidCreditCardPaymentEvent = new EventEmitter<VoidPaymentDialogRequest>();

  form: VoidPaymentDialogRequestForm;
  TenderTypeEnum = TenderTypeID;

  get CreditCardPaymentSelected(): string | null
  {
    return this.form?.value?.PaymentUID;
  }

  get allSelected(): boolean
  {
    return this.selection.selected.length !== 0 && this.selection.selected.length === this.payments.length;
  }


  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    private responsiveService: ResponsiveService,
    public manageLoadingService: ManageLoadingService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    const paymentUID = this.integratedCreditPayments?.length ? this.integratedCreditPayments[0].TrxUID : null;
    this.form = new VoidPaymentDialogRequestForm(this.checkDetails.PaymentRecordUID, this.checkDetails.CheckUID, [], paymentUID);
    this.form.markAllAsTouched();
    this.responsiveService.mediaBreakpoint.subscribe((screenSize: string) =>
    {
      this.isMobile = false;
      this.isMobile = (window.innerWidth <= 767 || (screenSize === 'xs' || screenSize === 'sm'));
    });
  }

  toggleMasterSelection($event): void
  {
    $event.preventDefault();
    $event.stopPropagation();

    if (this.allSelected)
    {
      this.selection.clear();
    } else
    {
      this.selection.select(...this.payments);
    }
  }

  itemChanged($event, item: PaymentsTendered): void
  {
    $event?.stopPropagation();
    $event?.preventDefault();
    this.selection?.toggle(item);
    const request = this.form.value;
    request.TrxIDs = this.selection.selected.map(a => a.TrxUID);
    this.form.setValue(request);
  }

  voidCreditCardPayment(): void
  {
    if (this.form.value.PaymentUID)
    {
      this.voidCreditCardPaymentEvent.emit(this.form.value);
    }
  }
}
