import { PaymentProcessConfiguration } from "../payment-configuration.model";
import { OrderStatus } from "./post-payment.model";

export class GetProcessRequest
{
  RefID: string;
}

export class GetProcessResponse
{
  Payment: PaymentProcess;
}

export class PaymentProcess
{
  OrderID: string;
  TransactionID: string;
  Title: string;
  IsSingleCheck: boolean;
  ShowGratuity: boolean;
  POSID: string;
  Description: string;
  Items: PaymentItem[];
  TenderType: TenderType;
  Printers: Array<Printer>;
  Configuration: PaymentProcessConfiguration;
  OrderStatus: OrderStatus;
  TrxType: TransactionType;
  IsReturn: boolean;
}

export class Printer
{
  Identifier: string;
  Name: string;
  PrinterModel: string;
  PrinterID: number;
  PrinterModelID: number;
  PrinterUID: string;
  SiteID: number;
  Type: PrinterType;
}

export enum PrinterType
{
  Primary,
  Backup
}

export enum TransactionType
{
  Normal = 0,
  Return
}


export class TenderType
{
  IntegratedCreditCard: boolean;
  IntegratedDebitCard: boolean;
  EnableTipsOnGiftCard: boolean;
  EnableReturntoGiftCard: boolean;
  Type: ProcessorType;
  CreditCardType: CreditCardType;
  PaymentMethods: PaymentMethods[];
}

export enum ProcessorType
{
  NotIntegrated,
  TEIPaymentSolution,
  // we will not support these on web pos so treat them as NotIntegrated.
  ETS,
  // we will not support these on web pos so treat them as NotIntegrated.
  Shift4
}


export class CreditCardType
{
  CreditCardID: string;
  Description: string;
  Prefix: string;
  Length: number;
  Account: string;
  Allowed: boolean;
}

export class PaymentMethods
{
  PaymentMethodID: number;
  DisplayName: string;
  Display: boolean;
  Account: string;
  ExchangeRate: number;
  IncludeInCash: boolean;
  KickDrawer: boolean;
  Type: PaymentMethodType;
  Amount: number;
  IsReturned: boolean;
  Remaining: number;
  CreditCardID: string;
  PaidTenderID: string;
  SourcePaymentUID: string;
  Options: Options[] = [];
}

export enum PaymentMethodType
{
  Normal,
  UserDefined
}

export class Options
{
  Account: string;
  Allowed: boolean;
  CreditCardID: string;
  Description: string;
  Length: string;
  Name: string;
  Prefix: string;
}

export class PaymentItem
{
  ItemID: string;
  Title: string;
  Remaining: number;
  Status: PaymentStatus;
  PaymentContact: Contact;
  Summary: ItemSummary;
  ErrorMessage: string;
  InValidTenderTypes?: Record<string, string>;
  Notes: Note[];
}

export class Note
{
  Label: string;
  Value: string;
  UserDisplayName: string;
  Icon: string;
  Type: NoteType;
  NoteDetails: NoteDetail[];
}
export class NoteDetail
{
  NoteDetailType: NoteDetailType;
  Value: boolean;
  Title: string;
}

export enum NoteDetailType
{
  FoodAndBeverageShowOnReceipt = 1
}

export enum NoteType
{

  FoodAndBeverageCheckNote = 1,
  FoodAndBeverageAdminNote = 2,
  RetailTrxNote = 3,
  RetailReceiptNote = 4
}

export enum PaymentDetailsType
{
  Note = 1
}

export enum PaymentStatus
{
  Unpaid,
  Paid,
  PartiallyPaid
}

export class Contact
{
  ContactID: string;
  MemberID: string;
  DisplayName: string;
  ProfilePicture: string;
  Email: string;
}

export class ItemSummary
{
  Items: SummaryDetailItem[];
  PaymentSummary: ItemPaymentSummary;
}


export class SummaryDetailItem
{
  ItemID: string;
  UofM: string;
  Title: string;
  Description: string;
  UnitPrice: number;
  DiscountAmount: number;
  Quantity: number;
  DiscountPercent: number;
  Status: ItemStatus;
  SubItems: SummaryDetailSubItem[];
}

export class ItemPaymentSummary
{
  SubTotal: number;
  Saved: number;
  Total: number;
  Tax: number;
  ExtraCosts: ExtraCost;
  PaymentsTendered: PaymentsTendered[];
}

export enum ItemStatus
{
  NewCheck = 1,
  SentToKitchen = 2,
  Deleted = 3,
  Printed = 4,
  InProcessOfPayment = 5,
  Paid = 6,
  OnAccountApproved = 7,
  OnCreditCardApproved = 8,
  OnGiftCardApproved = 9
}

export class SummaryDetailSubItem
{
  SubItemID: string;
  Title: string;
  Price: number;
  Type: SubItemType;
}

export class ExtraCost
{
  Title: string;
  Enable: boolean;
  Type: CostType;
  Tax: number;
  Percent: number;
  Total: number;

}

export class PaymentsTendered
{
  Amount: number;
  Gratuity: number;
  GratuityDescription: string;
  Status: PaymentsTenderedStatus;
  TenderTypeDescription: string;
  TenderTypeID: TenderTypeID;
  TransactionTime: Date;
  TrxUID: string;
  ContactID: string;
  AccountID: string;
  GiftCardID: number;
  IsIntegratedCCPayment: boolean;
}

export enum SubItemType
{
  MenuItem,
  Modifier,
  Alternative,
  Option
}

export enum CostType
{
  Gratuity,
  Surcharge
}

export enum PaymentsTenderedStatus
{
  Paid = 0,
  Voided = 1,
  Approved = 2
}

export enum TenderTypeID
{
  Cash = 1,
  CreditCard = 2,
  Check = 3,
  GiftCertificate = 4,
  MemberAccount = 5,
  OnEvent = 6,
  Debit = 7,
  PointsRedemption = 8,
  UD1 = 9,
  UD2 = 10,
  CashEqCertificate = 11,
  Voiding = 100
}
