import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IManageContactsPaymentService } from '../@intermediate/manage-contacts.interface';
import { VirtualCashEQsRequest, VirtualCashEQsResponse } from '../@intermediate/models/virtual-cash-eqs.model';

@Injectable()
// to do : we should change the "VirtualCashEQs" api address from ManageContacts it has confilict with "ManageContactsService" in contact project
export class ManageContactsPaymentService implements IManageContactsPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Office/Contacts/ManageContacts';
  }

  VirtualCashEQs(request: VirtualCashEQsRequest): Observable<VirtualCashEQsResponse>
  {
    const apiUrl = this.serviceUrl + '/VirtualCashEQs';
    return this.http.get<VirtualCashEQsResponse>(apiUrl, { params: request as any });
  }
}
