import { EventEmitter, Inject, Injectable, Output } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { SignalrMessagingClientService } from '@tei/core-signalr';
import { SnackBarService } from '@tei/material';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, finalize, map, mergeMap } from 'rxjs/operators';
import { IPaymentProcessGenericService } from '../@intermediate/IPaymentProcessGenericService.interface';
import { PaymentProcessConfiguration } from '../@intermediate/models/payment-configuration.model';
import { ProcessorType, TenderTypeID } from '../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest, PaymentResponse } from '../@intermediate/models/payment-process/post-payment.model';
import { PaymentVoidRequest } from '../@intermediate/models/payment-void/post-payment-void.model';
import { PostVoidCCPaymentRequest, PostVoidCCPaymentResponse } from '../@intermediate/models/payment-void/post-void-cc-payment.model';
import { IVoidPaymentService } from '../@intermediate/models/payment-void/void-payment.service.interface';
import { CheckDetails } from '../@intermediate/models/process.model';
import { ProcessService } from './process.service';

@Injectable({ providedIn: 'root' })
export class PaymentProcessGenericService implements IPaymentProcessGenericService
{
  inCompleteTransactionDialogue: boolean;
  returnUrl = "";
  selectedPrinterID? = null;
  cancelUrl = "";
  @Output() dialogUpdateMessageEvent = new EventEmitter();
  changeCheckDetailsSubject = new BehaviorSubject<CheckDetails>(new CheckDetails());
  configuration = new PaymentProcessConfiguration();

  constructor(private translate: TranslateService,
    @Inject('IVoidPaymentService') private voidPaymentService: IVoidPaymentService,
    private snackBarService: SnackBarService,
    @Inject('IProcessService') private processService: ProcessService,
    private route: ActivatedRoute,
    public manageLoadingService: ManageLoadingService,
    private signalRService: SignalrMessagingClientService
  ) { }

  ProcessPayment(request: PaymentRequest, dialogRef: MatDialogRef<any> = null,
    printAfterPayment = false, showSuccessMessage = true): Observable<PaymentResponse>
  {

    const checkDetails = this.changeCheckDetailsSubject.getValue();

    if (!request.PaymentInitiatorUID)
    {
      request.PaymentInitiatorUID = checkDetails.PaymentRecordUID;
      if (!request.PaymentInitiatorUID)
      {
        request.PaymentInitiatorUID = this.route.snapshot.paramMap.get('PaymentRecordUID');
      }
    }

    if (request.PaymentMethodID !== TenderTypeID.CreditCard || checkDetails?.TerminalTenderType?.Type === ProcessorType.NotIntegrated)
    {
      return this.ApplyPayment(request, showSuccessMessage, printAfterPayment, dialogRef);
    }

    this.manageLoadingService.startLoading();
    return this.signalRService.checkStatusAndForceToReconnect().pipe
      (
        finalize(() => this.manageLoadingService.finishLoading()),
        mergeMap(result =>
        {
          if (result)
          {
            return this.ApplyPayment(request, showSuccessMessage, printAfterPayment, dialogRef);
          }

          this.snackBarService.showInfo(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SIGNAL_R_ERROR'), '', null, 'left');
          throw new Error(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SIGNAL_R_ERROR'));
        })
      );

  }

  private ApplyPayment(request: PaymentRequest, showSuccessMessage: boolean, printAfterPayment: boolean, dialogRef: MatDialogRef<any, any>)
  {
    this.manageLoadingService.startLoading();
    const errorMessage = request.IsApproved ? this.translate.instant('PAYMENT_PROCESSOR.ON_ACCOUNT.COMMIT_ERROR') : this.translate.instant('PAYMENT_PROCESSOR.ON_ACCOUNT.APPROVE_PRINT_ERROR');
    return this.processService.ApplyPayment(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .pipe(map(result =>
      {
        if (result.Success)
        {
          if (showSuccessMessage)
          {
            if (request.Amount === 0)
            {
              this.snackBarService.showInfo(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.COMPLETED_TRANSACTIONS'), '', null, 'left');
            } else
            {
              this.snackBarService.showInfo(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SUCCESS_MESSAGE'), '', null, 'left');
            }
          }
          if (result?.PrintToFileUrl && printAfterPayment)
          {
            this.DownloadFile(result.PrintToFileUrl, result.TrxID + '.pdf');
          }
          this.dialogUpdateMessageEvent.emit();
          dialogRef?.close();
          return result;
        }

        if (result.ErrorMsg)
        {
          this.snackBarService.showError(result.ErrorMsg, '', null, 'left');
          return null;
        }

        this.snackBarService.showError(errorMessage, '', null, 'left');
      }));
  }

  voidPayments(voidPaymentRequest: PaymentVoidRequest): Observable<boolean>
  {
    this.manageLoadingService.startLoading();
    return this.voidPaymentService.Post(voidPaymentRequest)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .pipe(map(response =>
      {
        if (response.Success === true)
        {
          this.snackBarService.showSuccess(this.translate.instant('PAYMENT_PROCESSOR.VOID_PAYMENT.VOIDED_SUCCESSFULLY'), '', null, 'left');
          this.dialogUpdateMessageEvent.emit();
        }
        return response.Success;
      }), catchError(() =>
      {
        this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.VOID_PAYMENT.VOID_ERROR'), '', null, 'left');
        return of(false);
      }));
  }

  voidCreditCardPayment(voidPaymentRequest: PostVoidCCPaymentRequest): Observable<PostVoidCCPaymentResponse>
  {
    this.manageLoadingService.startLoading();
    return this.signalRService.checkStatusAndForceToReconnect().pipe
      (
        finalize(() => this.manageLoadingService.finishLoading()),
        mergeMap(result =>
        {
          if (result)
          {
            return this.voidCreditCardPaymentAfterCheckSignalR(voidPaymentRequest);
          }

          this.snackBarService.showInfo(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SIGNAL_R_ERROR'), '', null, 'left');
          throw new Error(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SIGNAL_R_ERROR'));
        })
      );

  }

  private voidCreditCardPaymentAfterCheckSignalR(voidPaymentRequest: PostVoidCCPaymentRequest)
  {
    this.manageLoadingService.startLoading();
    return this.voidPaymentService.CreditCard(voidPaymentRequest)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .pipe(map(response =>
      {
        return response;
      }), catchError(() =>
      {
        this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.VOID_PAYMENT.VOID_ERROR'), '', null, 'left');
        return of();
      }));
  }

  DownloadFile(url: string, fileName: string): void
  {
    const link = document.createElement('a');
    link.href = url;
    link.download = fileName + '.pdf';
    link.target = '_blank';
    link.click();
    window.URL.revokeObjectURL(url);
  }

  ReturnValidValue(value: string): number
  {
    return Number((value ?? '0.00').toString()?.replace(/[%]/g, ''));
  }
}



