import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetProcessRequest, GetProcessResponse } from '../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest, PaymentResponse } from '../@intermediate/models/payment-process/post-payment.model';
import { IProcessService } from '../@intermediate/process.interface';

@Injectable()
export class ProcessService implements IProcessService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/Process';
  }

  public Get(request: GetProcessRequest): Observable<GetProcessResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetProcessResponse>(apiUrl, { params: request as any });
  }

  ApplyPayment(request: PaymentRequest): Observable<PaymentResponse>
  {
    const apiUrl = this.serviceUrl + '/Payment';
    return this.http.post<PaymentResponse>(apiUrl, request);
  }
}
