import
{
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  SkipSelf,
  ViewChild
} from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor } from '@tei/core';
import { TeiCurrencyTranslateService } from "@tei/core-translate";
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../../@intermediate/IPaymentProcessGenericService.interface';
import { Account, CreditLimitType } from '../../../@intermediate/models/get-account.model';
import { Printer, TenderTypeID } from '../../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails } from '../../../@intermediate/models/process.model';
import { AccountQuickSearchComponent } from '../account-quick-search/account-quick-search.component';

@Component({
  selector: 'tei-common-payment-processor-on-account-approve-print',
  templateUrl: './on-account-approve-print.component.html',
  styleUrls: ['./on-account-approve-print.component.scss']
})
export class OnAccountApprovePrintComponent extends ModelDrivenFormGroupControlValueAccessor implements AfterViewInit, OnDestroy, OnInit
{
  @ViewChild('accountQuickSearch', { static: true }) private accountItemQuickSearch: AccountQuickSearchComponent;

  @Input() printerList: Printer[] = [];
  @Input() stepper: MatStepper;
  @Input() accountList = Array<Account>();
  @Input() form: UntypedFormGroup;
  @Input() orderID: number;
  @Input() selectedAccount: Account;
  @Input() tenderTypeID: TenderTypeID;
  @Input() checkDetails: CheckDetails;

  @Output() approvedApplyEvent = new EventEmitter();

  subscription: Subscription = new Subscription();
  currencySymbol: string;

  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private DialogRef: MatDialogRef<OnAccountApprovePrintComponent>,
    private currencyService: TeiCurrencyTranslateService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    private changeDetectorRef: ChangeDetectorRef,
    public manageLoadingService: ManageLoadingService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
  }

  ngAfterViewInit(): void
  {
    this.subscription.add(this.DialogRef.afterOpened().subscribe(() =>
    {
      this.onTotalApprovedBlur();

      if (!this.hasSelectedAccount())
      {
        this.accountItemQuickSearch?.loadAccount(this.accountList);
      }
    }));
    this.changeDetectorRef.detectChanges();
  }

  onlyNumber(event)
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  get formControls()
  {
    return this.form.controls;
  }

  onTotalApprovedBlur(): void
  {
    const remainingTotal = this.returnValidValue(this.formControls?.remainingTotal?.value);
    let totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    if (totalApproved > remainingTotal)
    {
      totalApproved = remainingTotal;
    }
    this.formControls?.totalApproved?.setValue(totalApproved.toFixed(2));

    this.validateTotalApprovedControl();
  }

  getPrinterName(printerID?: string): string
  {
    return this.printerList?.filter(item => item.PrinterID === Number(printerID))[0]?.Name;
  }

  private validateTotalApprovedControl()
  {
    const control = this.formControls?.totalApproved;
    control.setErrors(null);

    if (this.hasExceedError)
    {
      control.setErrors({ exceedError: true });
    }
    if (this.hasFullPaymentError)
    {
      control.setErrors({ fullPaymentError: true });
    }
    this.form.markAllAsTouched();
  }

  returnValidValue(value: string): number
  {
    if (!value)
    {
      value = '0.00';
    }

    return Number(value.toString().replace(/[%]/g, ''));
  }


  close(): void
  {
    this.DialogRef.close();
  }

  approvedPrintDone()
  {
    if (!this.selectedAccount || this.hasExceedError || this.hasFullPaymentError || (this.formControls.printerID.invalid && this.paymentProcessGenericService?.configuration?.OnAccountTenderTypeModel?.AllowPrint))
    {
      this.validateTotalApprovedControl();
      return;
    }

    const request = new PaymentRequest();
    request.PaymentItemID = this.checkDetails.CheckUID;
    request.PaymentMethodID = this.tenderTypeID;
    request.ContactID = this.checkDetails?.Contact?.ContactID;
    request.AccountID = this.selectedAccount?.AccountID;
    request.Amount = this.returnValidValue(this.formControls.paymentTotal.value);
    request.PrinterID = this.formControls.printerID.value;
    request.IsApproved = false;
    request.OrderID = this.checkDetails.OrderSeqID;
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(request, null, this.paymentProcessGenericService?.configuration?.OnAccountTenderTypeModel?.AllowPrint, false).subscribe(response =>
    {
      if (!response) { return; }
      this.approvedApplyEvent.emit({ selectedAccount: this.selectedAccount, result: response });
    }));
  }

  onSelectAccount(account: Account): void
  {
    this.selectedAccount = account;
    this.onTotalApprovedBlur();
  }

  get hasFullPaymentError(): boolean
  {
    return this.formControls?.totalApproved?.value < this.formControls?.remainingTotal?.value;
  }

  get exceedErrorMessage(): string
  {
    if (!this.selectedAccount) { return ''; }

    return this.selectedAccount.CreditLimitType === CreditLimitType.NoCredit ?
      'PAYMENT_PROCESSOR.ON_ACCOUNT.HAS_NO_CREDIT_ERROR' : 'PAYMENT_PROCESSOR.ON_ACCOUNT.EXCEED_SIMPLE_ERROR';
  }

  get hasExceedError(): boolean
  {
    if (!this.selectedAccount) { return false; }

    if (this.selectedAccount.CreditLimitType === CreditLimitType.Unlimited)
    {
      return false;
    }

    if (this.selectedAccount.CreditLimitType === CreditLimitType.NoCredit)
    {
      return true;
    }

    const paymentTotal = this.returnValidValue(this.formControls?.paymentTotal?.value);
    if (paymentTotal > this.selectedAccount.CreditLimitAmount) { return true; }
    return false;
  }

  validateForm(): boolean
  {
    return (!this.paymentProcessGenericService?.configuration?.OnAccountTenderTypeModel?.AllowPrint || this.formControls?.printerID?.valid) && this.hasSelectedAccount() && !this.hasExceedError && !this.hasFullPaymentError;
  }

  hasSelectedAccount(): boolean
  {
    if (!this.selectedAccount) { return false; }
    if (this.selectedAccount.AccountID === undefined || !this.selectedAccount.AccountID)
    { return false; }
    return true;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
