import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { AbstractControl, UntypedFormBuilder, UntypedFormGroup, ValidationErrors, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails, UDFPaymentMethodsDialogData } from '../../@intermediate/models/process.model';
@Component({
  selector: 'tei-common-payment-processor-udf-pay',
  templateUrl: './udf-pay.component.html',
  styleUrls: ['./udf-pay.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(UDFPayComponent)]
})
export class UDFPayComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  totalAmount = 0;
  checkDetails: CheckDetails;

  constructor(private formBuilder: UntypedFormBuilder, @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private DialogRef: MatDialogRef<UDFPayComponent>,
    public manageLoadingService: ManageLoadingService,
    @Inject(MAT_DIALOG_DATA) public UDFDialogData: UDFPaymentMethodsDialogData,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  getExactAmount(): void
  {
    const bucks = this.totalAmount * this.UDFDialogData.ExchangeRate;
    this.formControls.bucks.patchValue(bucks.toFixed(2));
    this.formControls.amount.patchValue(this.totalAmount.toFixed(2));
  }

  get formControls() { return this.form.controls; }

  ngOnInit(): void
  {
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(checkDetails =>
    {
      this.checkDetails = checkDetails;
    }));

    this.totalAmount = this.UDFDialogData.total;
    this.form = this.formBuilder.group({
      bucks: [0, [Validators.required, this.validateBucks.bind(this)]],
      exchangeRate: this.UDFDialogData.ExchangeRate,
      amount: [0, [Validators.required, this.validateAmount.bind(this)]]
    });
    this.bucksConversion();
  }

  validateBucks(control: AbstractControl): ValidationErrors | null {
    if (this.totalAmount < 0 && control.value >= 0) {
      return { mustBeNegative: true };
    }
    if (this.totalAmount >= 0 && control.value < 0) {
      return { mustBePositive: true };
    }
    return null;
  }
  validateAmount(control: AbstractControl): ValidationErrors | null {
    if (this.totalAmount < 0 && control.value >= 0) {
      return { mustBeNegative: true };
    }
    if (this.totalAmount >= 0 && control.value < 0) {
      return { mustBePositive: true };
    }
    return null;
  }
  bucksConversion(): void
  {
    const bucks = Number(this.formControls.bucks.value);
    const amount = Number(this.formControls.bucks.value) / this.UDFDialogData.ExchangeRate;
    const amountControl = this.formControls.amount;
    const bucksControl = this.formControls.bucks;
    amountControl.patchValue(amount.toFixed(2));
    bucksControl.patchValue(bucks.toFixed(2));
  }

  close(): void
  {
    this.DialogRef.close();
  }

  processPayment(): void
  {
    const request = new PaymentRequest();
    request.RedeemedPoints = 0.0;
    request.PaymentMethodID = this.UDFDialogData.PaymentMethodID;
    if (request.PaymentMethodID === 8)
    {
      request.RedeemedPoints = this.form.controls.amount.value * 100;
    }
    request.Amount = this.form.controls.amount.value;
    request.OrderID = this.checkDetails.OrderSeqID;
    request.CashBack = 0.0;
    request.PaymentItemID = this.checkDetails.CheckUID;
    request.ExchangeRate = this.form.controls.exchangeRate.value;
    this.paymentProcessGenericService.ProcessPayment(request, this.DialogRef, false).subscribe();
  }


  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}

