import { Component, EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { EventListComponent, EventSearch } from '@tei/office-event-management';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { Account, GetAccountsRequest } from '../../@intermediate/models/get-account.model';
import { GetRedeemablePointsResponse } from '../../@intermediate/models/get-redeemable-points.model';
import { PaymentMethodType, PaymentMethods, PaymentStatus, PaymentsTenderedStatus, Printer, TenderType, TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { CheckDetails, UDFPaymentMethodsDialogData } from '../../@intermediate/models/process.model';
import { AccountService } from '../../@services/account.service';
import { CashEqPayComponent } from '../cash-eq-pay/cash-eq-pay.component';
import { CashPayComponent } from '../cash-pay/cash-pay.component';
import { CashReturnComponent } from '../cash-return/cash-return.component';
import { CheckDialogComponent } from '../check-dialog/check-dialog.component';
import { CreditDebitComponent } from '../credit-debit/credit-debit.component';
import { GiftCardPayComponent } from '../gift-card-pay/gift-card-pay.component';
import { GiftCardReturnComponent } from '../gift-card-pay/gift-card-return/gift-card-return.component';
import { OnAccountPayComponent } from '../on-account-pay/on-account-pay.component';
import { OnEventComponent } from '../on-event/on-event.component';
import { PointPayDialogComponent } from '../point-pay-dialog/point-pay-dialog.component';
import { UDFPayComponent } from '../udf-pay/udf-pay.component';

@Component({
  selector: 'tei-common-payment-processor-payment-method',
  templateUrl: './payment-method.component.html',
  styleUrls: ['./payment-method.component.scss']
})
export class PaymentMethodComponent implements OnChanges, OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  @Input() total: number;
  @Input() showGratuity: boolean;
  @Input() tenderType: TenderType;
  @Input() checkDetails: CheckDetails;
  @Input() printerList: Printer[] = [];
  @Input() enableTipsOnGiftCard: boolean;
  @Input() contactPoints: GetRedeemablePointsResponse;
  @Input() isReturn: boolean = false;
  @Input() enableReturnToGiftCard: boolean = false;

  @Output() openContactDialogEvent = new EventEmitter();

  methodList: PaymentMethods[] = [];
  UDFPaymentMethods: PaymentMethods[];
  hasARAccount = false;
  accountList = new Array<Account>();
  invalidTenderType?: Record<string, string>;
  constructor(
    private dialog: MatDialog,
    @Inject('IAccountService') private accountService: AccountService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    private snackBarService: SnackBarService) { }

  ngOnInit(): void
  {
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(checkDetails =>
    {
      this.checkDetails = checkDetails;
      this.invalidTenderType = this.checkDetails?.PaymentItem?.[0]?.InValidTenderTypes;

      if (!this.checkDetails?.Contact)
      {
        this.hasARAccount = false;
        this.accountList = [];
        return;
      }
      this.loadAccounts();
    }));

    this.loadAccounts();
  }

  loadAccounts(): void
  {
    if (!this.checkDetails?.Contact) { return; }
    this.manageLoadingService.startLoading();
    this.subscription.add(this.accountService.Get({ ContactID: this.checkDetails?.Contact?.ContactID } as GetAccountsRequest)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe(result =>
      {
        this.accountList = result.Accounts;
        this.hasARAccount = this.accountList.some(p => p.AllowAR === true);
      }));
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (changes.tenderType && changes.tenderType.currentValue)
    {
      const tender = changes.tenderType.currentValue;

      // this.isReturn = tender.EnableReturnToGiftCard != null ? true : false;

      this.createMethodList();
    }
  }

  createMethodList(): void
  {
    const normalPaymentMethods = this.tenderType?.PaymentMethods.filter(item => item.Type === PaymentMethodType.Normal) || [];
    this.UDFPaymentMethods = this.tenderType?.PaymentMethods.filter(item => item.Type === PaymentMethodType.UserDefined);
    const firstThreeUDFPaymentMethods = this.UDFPaymentMethods?.slice(0, 3) || [];

    this.methodList = [...normalPaymentMethods, ...firstThreeUDFPaymentMethods];
  }

  createDialog(paymentMethod: PaymentMethods, tenderType: TenderType)
  {
    const errorMessage = this.checkPaymentStateError(paymentMethod.PaymentMethodID);
    if (errorMessage)
    {
      this.snackBarService.showError(errorMessage, '', null, 'left');
      return;
    }

    if (this.isReturn && paymentMethod.IsReturned)
    {
      return;
    }
    if (!this.isReturn)
    {
      if (this.UDFPaymentMethods.some(UDFPaymentMethod => UDFPaymentMethod.PaymentMethodID === paymentMethod.PaymentMethodID))
      {
        return this.onUDFPaymentMethods(this.UDFPaymentMethods.find(UDFPaymentMethod => UDFPaymentMethod.PaymentMethodID === paymentMethod.PaymentMethodID));
      }
      switch (paymentMethod.PaymentMethodID)
      {
        case TenderTypeID.Cash:
          return this.onPayByCash();
        case TenderTypeID.CreditCard:
          return this.openCreditDebitDialog(paymentMethod);
        case TenderTypeID.Check:
          return this.openCheckDialog();
        case TenderTypeID.GiftCertificate:
          return this.onPayByGiftCard();
        case TenderTypeID.MemberAccount:
          return this.onPayByAccount();
        case TenderTypeID.PointsRedemption:
          return this.onPayByPoints();
        case TenderTypeID.CashEqCertificate:
          return this.onPayByCashEQ();
        case TenderTypeID.OnEvent:
          return this.openOnEvent();
        default:
          return;
      }

    }
    else
    {
      paymentMethod.PaymentMethodID == TenderTypeID.GiftCertificate
        ? this.onReturnByGiftCard(paymentMethod) : this.onReturnByTenderType(paymentMethod, tenderType);
    }

  }




  openDialog(paymentMethod: PaymentMethods, tenderType: TenderType)
  {
    const dialog = this.createDialog(paymentMethod, tenderType);
    if (dialog)
    {
      this.subscription.add(dialog.backdropClick().subscribe(() =>
      {
        dialog.disableClose = this.manageLoadingService.isLoading;
        return !dialog.disableClose;
      }));
    }
  }

  onPayByPoints()
  {
    return this.dialog.open(PointPayDialogComponent, {
      disableClose: true,
      panelClass: ['point-pay-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        contactPoints: this.contactPoints,
        total: this.total
      }
    });
  }


  checkPaymentStateError(tenderTypeId: number): string | null
  {

    if (tenderTypeId === TenderTypeID.OnEvent && this.checkDetails.Status === PaymentStatus.PartiallyPaid)
    {
      return this.translate.instant('PAYMENT_PROCESSOR.ON_EVENT.METHOD_NOT_ALLOWED');
    }

    if (!this.checkDetails)
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_CHECK_SELECTED_ERROR');
    }

    if (this.checkDetails?.CheckErrorMessage)
    {
      return this.translate.instant(this.checkDetails.CheckErrorMessage);
    }

    if (this.checkDetails?.PaymentsTendered?.length > 0)
    {
      const errorMessage = this.checkForApprovedTenderType([TenderTypeID.CreditCard, TenderTypeID.MemberAccount], tenderTypeId);
      if (errorMessage) { return errorMessage; }

      const cashPaymentsTendered = this.checkDetails?.PaymentsTendered?.find(p => p.TenderTypeID !== TenderTypeID.MemberAccount
        && p.Status !== PaymentsTenderedStatus.Voided);
      if (cashPaymentsTendered && tenderTypeId === TenderTypeID.MemberAccount)
      {
        return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ACCOUNT_PAYMENT_NOT_ALLOWED_ERROR');
      }
    }
    else if (this.checkDetails?.Remaining === 0 && this.total === 0 && tenderTypeId !== TenderTypeID.Cash)
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ACCOUNT_PAYMENT_NOT_ALLOWED_ERROR');
    }

    if (tenderTypeId === TenderTypeID.MemberAccount)
    {
      return this.onAccountValidation(tenderTypeId);
    }

    const errMessage = this.PointValidation(tenderTypeId);
    if (errMessage) { return errMessage; }

    if (this.total === 0)
    {
      if (tenderTypeId === TenderTypeID.GiftCertificate && this.showGratuity && this.enableTipsOnGiftCard
        && this.checkDetails?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.GiftCertificate && p.Status === PaymentsTenderedStatus.Approved))
      {
        return null;
      }

      if (this.checkDetails.Status === PaymentStatus.Paid || tenderTypeId !== TenderTypeID.Cash)
      {
        return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ALREADY_PAID_ERROR', { title: this.checkDetails?.Title ?? '-' });
      }
    }

    if (tenderTypeId !== TenderTypeID.GiftCertificate)
    {
      // Some conditions for enable/disable payment method
      return this.onGiftCardValidation(tenderTypeId);
    }
    return null;
  }

  PointValidation(tenderTypeId: TenderTypeID): string
  {
    if (tenderTypeId === TenderTypeID.PointsRedemption)
    {
      if (this.checkDetails?.PaymentsTendered?.some(p => p.Status === PaymentsTenderedStatus.Paid && p.TenderTypeID === TenderTypeID.PointsRedemption))
      {
        if (this.checkDetails.isSingleCheck)
        {
          return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SINGLE_CHECK_PONT_PAID_ERROR', { title: this.checkDetails.Title ?? '-' });
        } else
        {
          return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.CHECK_PONT_PAID_ERROR', { title: this.checkDetails.Title ?? '-' });
        }
      }

      if (this.checkDetails?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.MemberAccount))
      {
        return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PAYMENT_NOT_ALLOWED_ERROR');
      }

      if (this.total !== 0)
      {
        if (!this.checkDetails?.Contact?.ContactID)
        {
          return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_CONTACT_ASSIGNED');
        }
        if (!this.contactPoints?.AvailablePoints)
        {
          return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_CONTACT_POINTS');
        }
        if (!this.contactPoints?.RedeemablePoints)
        {
          return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_CONTACT_PURCHASE_POINTS');
        }
      }
    }
    return null;
  }

  checkForApprovedTenderType(theTenderType: TenderTypeID[], tenderTypeId: number): string
  {
    let errorMessage = '';
    theTenderType?.forEach(tenderType =>
    {
      const paymentsTendered = this.checkDetails?.PaymentsTendered?.find(p => p.TenderTypeID === tenderType
        && p.Status === PaymentsTenderedStatus.Approved);

      if (paymentsTendered && tenderTypeId !== tenderType)
      {
        const displayName = this.methodList.find(p => p.PaymentMethodID === paymentsTendered.TenderTypeID)?.DisplayName;
        errorMessage = this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PAYMENT_METHOD_WRONG_ERROR', { paymentMethod: displayName });
        return;
      }
    });
    return errorMessage;
  }

  onGiftCardValidation(tenderTypeId: number): string | null
  {
    if (this.checkDetails?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.GiftCertificate && p.Status === PaymentsTenderedStatus.Approved)
      && this.enableTipsOnGiftCard && this.showGratuity && tenderTypeId !== TenderTypeID.GiftCertificate)
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.HAVE_UNCOMMITTED_GIFT_CARD');
    }
    return null;
  }

  onAccountValidation(tenderTypeId: number): string | null
  {
    if (this.invalidTenderType)
    {
      return this.invalidTenderType["MemberAccount"];
    }
    if (this.checkDetails?.PaymentsTendered?.some(p => p.Status === PaymentsTenderedStatus.Paid))
    {
      if (tenderTypeId !== TenderTypeID.MemberAccount)
      {
        return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PAYMENT_NOT_ALLOWED_ERROR');
      }
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ALREADY_PAID_ERROR', { title: this.checkDetails.Title ?? '-' });
    }

    if (this.checkDetails?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.MemberAccount && p.Status === PaymentsTenderedStatus.Approved)
      && this.enableTipsOnGiftCard && this.showGratuity && tenderTypeId !== TenderTypeID.MemberAccount)
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.HAVE_UNCOMMITTED_ON_ACCOUNT');
    }
    return null;
  }

  onPayByCash()
  {
    return this.dialog.open(CashPayComponent, {
      disableClose: true,
      panelClass: ['cash-pay-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: this.total
    });
  }

  onPayByCashEQ()
  {
    return this.dialog.open(CashEqPayComponent, {
      disableClose: true,
      panelClass: ['cash-eq-pay-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: this.checkDetails
    });
  }

  onUDFPaymentMethods(paymentMethod: PaymentMethods)
  {
    const data: UDFPaymentMethodsDialogData = {
      total: this.total, ExchangeRate: paymentMethod.ExchangeRate,
      PaymentMethodID: paymentMethod.PaymentMethodID,
      DisplayName: paymentMethod.DisplayName,
    };

    return this.dialog.open(UDFPayComponent, {
      disableClose: true,
      panelClass: ['UDF-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data
    });
  }

  openCheckDialog()
  {
    return this.dialog.open(CheckDialogComponent, {
      disableClose: true,
      panelClass: ['check-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        total: this.total,
        checkUID: this.checkDetails.CheckUID,
        orderSeqID: this.checkDetails.OrderSeqID
      }
    });
  }

  openCreditDebitDialog(paymentMethod: PaymentMethods = null)
  {
    return this.dialog.open(CreditDebitComponent, {
      disableClose: true,
      panelClass: ['credit-debit-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        checkUID: this.checkDetails.CheckUID,
        total: this.total,
        printerList: this.printerList,
        tenderType: this.tenderType,
        paymentMethod: paymentMethod
      }
    });
  }

  getPayAmountByTenderType(paymentMethod: PaymentMethods): string
  {
    if (!this.isReturn)
    {
      return this.total.toFixed(2);
    }
    else
    {
      return Math.max(this.total, -paymentMethod.Amount).toFixed(2);
    }
  }



  onPayByGiftCard()
  {
    return this.dialog.open(GiftCardPayComponent, {
      disableClose: true,
      panelClass: ['gift-card-pay-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        total: this.total.toFixed(2),
        showGratuity: this.showGratuity,
        printerList: this.printerList,
        checkDetails: this.checkDetails,
        enableTipsOnGiftCard: this.enableTipsOnGiftCard
      },
      autoFocus: false
    });
  }

  onReturnByGiftCard(paymentMethod: PaymentMethods)
  {
    return this.dialog.open(GiftCardReturnComponent, {
      disableClose: true,
      panelClass: ['gift-card-return-dialog'],
      width: '400px',
      data: {
        checkDetails: this.checkDetails,
        totalAmount: this.getPayAmountByTenderType(paymentMethod)
      },
      autoFocus: false
    });
  }

  onReturnByTenderType(paymentMethod: PaymentMethods, tenderType: TenderType)
  {
    if (!paymentMethod.CreditCardID?.length)
    {
      return this.dialog.open(CashReturnComponent, {
        disableClose: true,
        panelClass: ['cash-return-dialog'],
        width: '400px',
        data: {
          totalAmount: this.getPayAmountByTenderType(paymentMethod),
          paymentMethod: paymentMethod,
          IntegratedCreditCard: tenderType.IntegratedCreditCard,
          IntegratedDebitCard: tenderType.IntegratedDebitCard
        },
        autoFocus: false
      });
    } else
    {
      return this.dialog.open(CreditDebitComponent, {
        disableClose: true,
        panelClass: ['credit-debit-dialog', 'o-dialog-md-fullscreen'],
        width: '400px',
        data: {
          checkUID: this.checkDetails.CheckUID,
          total: this.total,
          printerList: this.printerList,
          tenderType: this.tenderType,
          paymentMethod: paymentMethod,
          IntegratedCreditCard: tenderType.IntegratedCreditCard,
          IntegratedDebitCard: tenderType.IntegratedDebitCard
        }
      });
    }
  }

  onPayByAccount()
  {
    if (!this.checkDetails || !this.checkDetails?.Contact || !this.checkDetails?.Contact?.ContactID)
    {
      this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SELECT_CONTACT'), '', null, 'left');
      this.openContactDialogEvent.emit();
      return;
    }

    if (!this.hasARAccount)
    {
      this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_ACCOUNT_ERROR'), '', null, 'left');
      return;
    }

    return this.dialog.open(OnAccountPayComponent, {
      disableClose: true,
      panelClass: ['on-account-pay-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        total: this.total,
        accountList: this.accountList,
        showGratuity: this.showGratuity,
        printerList: this.printerList,
        checkDetails: this.checkDetails
      }
    });
  }

  openOnEvent(): void
  {
    const dialog = this.dialog.open(EventListComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: {
        isEventRegistration: false,
        searchHeaderConfig: {
          startDate: new Date(),
          endDate: new Date()
        }
      }
    });
    this.subscription.add(dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.openOnEventPayment(result);
      }
    }));
  };

  openOnEventPayment(event: EventSearch): void
  {
    const dialog = this.dialog.open(OnEventComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: {
        EventName: event.EventName,
        PaymentAmount: this.total,
        EventUID: event.EventUID
      }
    });
  }

  hasGiftCardMethod(): boolean
  {
    return this.methodList?.some(a => a.PaymentMethodID === TenderTypeID.GiftCertificate);
  }


  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
