import { AfterViewInit, Component, ElementRef, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, SkipSelf, ViewChild } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { Observable, Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IGiftCardService } from './../../../@intermediate/gift-card.interface';
import { GetGiftCardRequest, GetGiftCardResponse, SelectedData } from './../../../@intermediate/models/get-gift-card.model';
import { Printer, PrinterType, TenderTypeID } from './../../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest, PaymentResponse } from './../../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails } from './../../../@intermediate/models/process.model';
import { PaymentProcessGenericService } from './../../../@services/payment-process-generic.service';

@Component({
  selector: 'tei-common-payment-processor-gift-card-approve',
  templateUrl: './gift-card-approve.component.html',
  styleUrls: ['./gift-card-approve.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(GiftCardApproveComponent)]
})
export class GiftCardApproveComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, AfterViewInit, OnDestroy
{
  @ViewChild('CardNumberInput', { read: ElementRef }) CardNumberInput!: ElementRef;
  subscription: Subscription = new Subscription();
  @Input() printerList: Printer[] = [];
  @Input() totalApproved: number;
  @Input() stepper: MatStepper;
  @Input() showGratuity: boolean;
  @Input() enableTipsOnGiftCard: boolean;
  @Input() checkDetails: CheckDetails;

  @Output() selectedDataEmitter = new EventEmitter<SelectedData>();
  @Output() closeEmitter = new EventEmitter<void>();

  form: UntypedFormGroup;
  cardIsValid = false;
  allowApprove: boolean;
  allowPrint: boolean;
  private previousCardNumber: string | null = null;
  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject('IGiftCardService') private service: IGiftCardService,
    private formBuilder: UntypedFormBuilder,
    public manageLoadingService: ManageLoadingService,
    @Inject('IPaymentProcessGenericService') private paymentProcessGenericService: PaymentProcessGenericService,
    public translate: TranslateService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    const defaultPrinter = this.printerList.filter(printer => printer.Type === PrinterType.Primary);
    this.allowApprove = this.paymentProcessGenericService.configuration.GiftCardTenderTypeModel.AllowApprove;
    this.allowPrint = this.paymentProcessGenericService.configuration.GiftCardTenderTypeModel.AllowPrint;
    this.form = this.formBuilder.group({
      totalApproved: [this.totalApproved, [Validators.required]],
      remainingTotal: [this.totalApproved],
      cardNumber: ['', [Validators.required]],
      balance: ['0.00'],
      printerID: [defaultPrinter.length > 0 ? defaultPrinter[0].PrinterID : null, [Validators.required]],
      giftCardUID: ['']
    });
    this.form.markAllAsTouched();
  }
  ngAfterViewInit(): void
  {
    setTimeout(() =>
    {
      this.CardNumberInput.nativeElement.focus();
    });
  }

  onCardNumberBlurred(): void
  {
    const value = this.form.get('cardNumber')?.value;

    if (value && value !== this.previousCardNumber)
    {
      this.onBarcodeScanned(value);
    }
  }
  onCardNumberEntered(): void
  {
    this.CardNumberInput.nativeElement?.blur();
  }
  getPrinterName(printerID?: string): string
  {
    return this.printerList?.filter(item => item.PrinterID === Number(printerID))[0]?.Name;
  }

  onLoadBalanceOfCardNumber(selectedData?: SelectedData): void
  {
    this.form.updateValueAndValidity();
    const request = new GetGiftCardRequest();
    request.GiftCardID = this.formControls?.cardNumber?.value;

    if (!request.GiftCardID)
    {
      return;
    }

    this.cardIsValid = false;
    this.manageLoadingService.startLoading();
    this.subscription.add(this.service.Get(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetGiftCardResponse) =>
      {
        if (response.GiftCardUID === '00000000-0000-0000-0000-000000000000')
        {
          this.formControls.cardNumber.setErrors({ notFound: true });
        } else
        {
          this.cardIsValid = true;
          this.formControls.cardNumber.setErrors(null);
          this.form.patchValue({
            balance: response.RemainingAmount.toFixed(2),
            giftCardUID: response.GiftCardUID
          });
          if (selectedData)
          {
            selectedData.Balance = this.formControls.balance.value;
            selectedData.GiftCardUID = response.GiftCardUID;
            this.selectedDataEmitter.emit(selectedData);
            if (this.enableTipsOnGiftCard)
            {
              this.stepper.next();
            }
          }
        }
      },
      complete: () => this.checkPaymentTotalValidation()
    }));
  }

  get formControls()
  {
    return this.form.controls;
  }

  onChangeCardNumber(): void
  {
    this.cardIsValid = false;
    this.formControls.balance.setValue('0.00');
  }

  onBarcodeScanned(scannedString: string): void
  {
    if (!scannedString)
    {
      return;
    }
    if (this.convertedBarcode(scannedString))
    {

      this.formControls?.cardNumber.setValue(this.convertedBarcode(scannedString), { emitEvent: false });
      this.previousCardNumber = this.convertedBarcode(scannedString);
    } else
    {
      this.previousCardNumber = scannedString;
    }
    this.onLoadBalanceOfCardNumber();

  }

  convertedBarcode(input: string): string | null
  {
    const teggcIndex = input.indexOf("TEGGC");
    if (teggcIndex === -1)
    {
      return null;
    }

    const firstCaretIndex = input.indexOf("^", teggcIndex);
    if (firstCaretIndex === -1)
    {
      return null;
    }
    const secondCaretIndex = input.indexOf("^", firstCaretIndex + 1);
    if (secondCaretIndex === -1)
    {
      return null;
    }

    return input.substring(firstCaretIndex + 1, secondCaretIndex);
  }

  onlyNumber(event)
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  onTotalApprovedBlur(): void
  {
    const remainingTotal = this.returnValidValue(this.formControls?.remainingTotal?.value);
    let totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    if (totalApproved > remainingTotal)
    {
      totalApproved = remainingTotal;
    }
    this.formControls?.totalApproved?.setValue(totalApproved.toFixed(2));

    this.checkPaymentTotalValidation();
  }

  returnValidValue(value: string): number
  {
    if (!value)
    {
      value = '0.00';
    }

    return Number(value.toString().replace(/[%]/g, ''));
  }

  private checkPaymentTotalValidation(): boolean
  {
    const totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    const balance = this.returnValidValue(this.formControls?.balance?.value);
    this.formControls.totalApproved.setErrors(null);
    this.form.markAllAsTouched();
    if (totalApproved > balance)
    {
      this.formControls.totalApproved.setErrors({ exceedError: true });
      return true;
    }
    return false;
  }

  onApprove(): void
  {
    if (this.formInValid || !this.formControls?.giftCardUID?.value || this.manageLoadingService.isLoading)
    {
      return;
    }

    const totalApproved = this.returnValidValue(this.formControls.totalApproved.value);
    const remainingTotal = this.returnValidValue(this.formControls.remainingTotal.value);
    this.manageLoadingService.startLoading();
    this.subscription.add(this.ProcessPayment(null, !this.allowApprove, this.allowPrint)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response) =>
      {
        if (!response?.Success) { return; }

        if (!this.enableTipsOnGiftCard)
        {
          this.closeEmitter.emit();
        }

        if (!this.showGratuity && this.allowApprove)
        {
          if (remainingTotal <= totalApproved)
          {
            this.subscription.add(this.ProcessPayment(response?.TrxID, true, false).subscribe(result =>
            {
              if (result?.Success)
              {
                this.closeEmitter.emit();
              }
            }));
          } else
          {
            this.closeEmitter.emit();
          }
        } else if (this.allowApprove)
        {
          const selectedData = new SelectedData();
          selectedData.CardNumber = this.formControls.cardNumber.value;
          selectedData.Balance = this.formControls.balance.value;
          selectedData.TotalApproved = this.formControls.totalApproved.value;
          selectedData.TransactionID = response?.TrxID;
          selectedData.GiftCardUID = this.formControls.giftCardUID.value;
          this.onLoadBalanceOfCardNumber(selectedData);
        } else
        {
          this.closeEmitter.emit();
        }
      }));
  }

  ProcessPayment(transactionID: string, isApproved: boolean, needPrint: boolean): Observable<PaymentResponse>
  {
    if (!this.formControls?.giftCardUID?.value) { return; }

    const request = new PaymentRequest();

    request.ContactID = this.checkDetails?.Contact?.ContactID;
    request.PrinterID = this.formControls?.printerID?.value;
    if (request.PrinterID && this.printerList.length)
    {
      request.PrinterID = this.printerList[0].PrinterID;
    }

    request.PaymentItemID = this.checkDetails.CheckUID;
    request.PaymentMethodID = TenderTypeID.GiftCertificate;
    if (transactionID)
    {
      request.ApprovedTransactionID = transactionID;
    }
    request.Amount = this.returnValidValue(this.formControls?.totalApproved?.value);
    request.IsApproved = isApproved;
    request.OrderID = this.checkDetails.OrderSeqID;
    request.GiftCardID = this.formControls?.giftCardUID?.value;
    this.manageLoadingService.startLoading();
    return this.paymentProcessGenericService.ProcessPayment(request, null, needPrint).pipe(finalize(() => this.manageLoadingService.finishLoading()));
  }

  get paymentButtonTitle(): string
  {
    if (this.formInValid)
    {
      return 'PAYMENT_PROCESSOR.GIFT_CARD.DETAILS_REQUIRED';
    }

    if (this.allowApprove && this.allowPrint)
    {
      return 'PAYMENT_PROCESSOR.GIFT_CARD.APPROVE_PRINT';
    }
    return 'PAYMENT_PROCESSOR.GIFT_CARD.APPLY_PAYMENT';
  }

  get formInValid(): boolean
  {
    if (this.allowApprove && this.allowPrint)
    {
      return this.form.invalid;
    }
    return this.formControls.cardNumber.invalid || !this.cardIsValid || this.formControls.totalApproved.invalid;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
