import { FormControl } from '@angular/forms';
import { TenderTypeID } from './get-process.model';

export class PaymentRequest
{
  OrderID: string;
  PaymentItemID: string;
  PaymentInitiatorUID: string;
  ConnectionID: string;
  PaymentMethodID: TenderTypeID;
  Amount: number;
  ContactID?: string;
  AccountID?: string;
  CashBack: number;
  ExchangeRate?: number;
  GiftCardID?: string;
  ExternalProcessorTransactionID: string;
  IsApproved?: boolean;
  ApprovedTransactionID?: string;
  RedeemedPoints: number;
  TipAmount?: number;
  CashEqItemUID?: string;
  CCName: string;
  PrinterID?: number;
  IntegratedCreditCard?: boolean;
  IntegratedDebitCard?: boolean;
  EventUID?: string;
  PaymentUID?: string;
}

export class PaymentResponse
{
  public Success: boolean;
  public TrxID?: string;
  public RemainingAmount: number;
  public OrderStatus: OrderStatus;
  public PaymentItemStatus: CheckStatus;
  public PrintToFileUrl?: string;
  public ErrorMsg?: string;
  public PaymentDeviceID?: string;
}

export enum CheckStatus
{
  NewCheck = 1,
  SentToKitchen = 2,
  Deleted = 3,
  Printed = 4,
  InProcessOfPayment = 5,
  Paid = 6,
  OnAccountApproved = 7,
  OnCreditCardApproved = 8,
  OnGiftCardApproved = 9
}

export enum OrderStatus
{
  InProgress = 1,
  Paying = 2,
  Cancelled = 3,
  SentToKitchen = 4,
  Completed = 5,
  Reactivated = 6
}
export interface CashReturnFrom
{
  TotalAmount: FormControl<number>;
}

export interface GiftCardReturnFrom
{
  cardNumber?: FormControl<string>;
  giftCardUID?: FormControl<string>;
  totalAmount: FormControl<number>;
  balance?: FormControl<string>;
}
