import { Inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryEnum, EventTypeEnum, GenericEventModel, GenericEventService } from '@tei/core';
import { IPaymentProcessGenericService } from '../@intermediate/IPaymentProcessGenericService.interface';

@Injectable()
export class PaymentNavigationService
{
  successUrl = '';
  failedUrl = '';

  constructor(
    private router: Router,
    private genericEventService: GenericEventService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService
  )
  {
  }

  getURL(): void
  {
    if (this.paymentProcessGenericService.returnUrl)
    {
      this.successUrl = this.paymentProcessGenericService.returnUrl + '?success=true';
      this.failedUrl = this.paymentProcessGenericService.returnUrl + '?failed=true';
    } else
    {
      this.successUrl = this.paymentProcessGenericService.configuration.SuccessUrl;
      this.failedUrl = this.paymentProcessGenericService.configuration.FailedUrl;
    }
  }

  changeModule(): void
  {
    this.genericEventService.onChangeGenericEvent({
      module: { Category: CategoryEnum.Pos, Name: 'common-payment-processor' },
      eventType: EventTypeEnum.onChangeSideBarVisibility,
      data: { sideBarVisible: true }
    } as GenericEventModel);
  }

  success(): void
  {
    this.getURL();
    this.changeModule();
    this.router.navigateByUrl(this.successUrl);
  }

  cancelByUser(): void
  {
    this.getURL();
    this.changeModule();
    this.router.navigateByUrl(this.failedUrl);
  }

  failedOnInitialize(): void
  {
    this.getURL();
    this.changeModule();
    this.router.navigateByUrl(this.failedUrl);
  }

}
