import { Location } from '@angular/common';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StorageService } from '@tei/core';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { IInitializeService } from '../../@intermediate/initialize.interface';
import { GetInitializeRequest, GetInitializeResponse, PaymentInitiator, REFERENCE_KEY_NAME, ReferenceModel } from '../../@intermediate/models/initialize.model';

@Component({
  selector: 'tei-common-payment-processor-initial',
  templateUrl: './initialize.component.html',
  styleUrls: ['./initialize.component.css']
})
export class InitializeComponent implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  state: string;
  selectedCheckUID: string;

  constructor(
    private storageService: StorageService,
    private route: ActivatedRoute,
    private router: Router,
    private location: Location,
    @Inject('IInitializeService') private paymentInitializeService: IInitializeService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    // const currentNavigation = this.router.getCurrentNavigation();

    const currentNavigationState: any = location.getState();
    if (currentNavigationState)
    {
      paymentProcessGenericService.returnUrl = currentNavigationState?.returnUrl;
      paymentProcessGenericService.inCompleteTransactionDialogue = currentNavigationState?.inCompleteTransactionDialogue;
    }
  }

  ngOnInit()
  {
    const request: GetInitializeRequest =
    {
      Initiator: PaymentInitiator[this.route.snapshot.paramMap.get('Initiator')],
      RefID: this.route.snapshot.paramMap.get('RefID')
    };

    const subRefID = this.route.snapshot.paramMap.get('SubRefID');
    this.selectedCheckUID = this.route.snapshot.queryParamMap.get('checkUID');

    if (subRefID && subRefID !== null && subRefID !== '')
    {
      request.SubRefID = this.route.snapshot.paramMap.get('SubRefID');
    }

    this.storageService.Set('initiator', request.Initiator);

    this.load(request);
  }

  load(request: GetInitializeRequest)
  {
    const reference: ReferenceModel = {
      id: request.RefID,
      subId: request.SubRefID,
      selectedCheckUID: this.selectedCheckUID
    };
    this.storageService.Set(REFERENCE_KEY_NAME, reference);

    this.subscription.add(this.paymentInitializeService.Get(request).subscribe((response: GetInitializeResponse) =>
    {
      this.router.navigate(['../../../../process', response.PaymentRecordUID], { relativeTo: this.route });
    }));
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
