import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IGiftCardService } from '../@intermediate/gift-card.interface';
import { GetGiftCardRequest, GetGiftCardResponse } from '../@intermediate/models/get-gift-card.model';

@Injectable()
export class GiftCardService implements IGiftCardService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/GiftCards';
  }
  Get(request: GetGiftCardRequest): Observable<GetGiftCardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetGiftCardResponse>(apiUrl, { params: request as any });

  }
}
