import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';
import { CashItem } from '../../@intermediate/models/process.model';

@Component({
  selector: 'tei-common-payment-processor-cash-pay',
  templateUrl: './cash-pay.component.html',
  styleUrls: ['./cash-pay.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(CashPayComponent)]
})
export class CashPayComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  totalAmount = 0;
  changeAmount = 0;
  cashItems: CashItem[] = [
    { Id: 1, Amount: 100 },
    { Id: 2, Amount: 50 },
    { Id: 3, Amount: 20 },
    { Id: 4, Amount: 10 },
    { Id: 5, Amount: 5 },
    { Id: 6, Amount: 2 },
    { Id: 7, Amount: 1 },
  ];
  currencySymbol: string;

  get isPayButtonDisable(): boolean
  {
    const checkDetails = this.paymentProcessGenericService?.changeCheckDetailsSubject?.value;
    return this.form.invalid ||
      ((Number(this.form.controls.cash.value) <= 0 || this.totalAmount <= 0) && checkDetails?.Total > 0) || ((this.totalAmount !== 0 || checkDetails?.Total !== 0) && Number(this.form.controls.cash.value) === 0);
  }

  constructor(private formBuilder: UntypedFormBuilder, @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private DialogRef: MatDialogRef<CashPayComponent>,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    @Inject(MAT_DIALOG_DATA) public cashPayDialogData: number,
    private currencyService: TeiCurrencyTranslateService,
    public manageLoadingService: ManageLoadingService)
  {
    super(errorStateMatcher);
  }

  calculate(cashItem: CashItem): void
  {
    const cash = Number(this.formControls.cash.value);
    const addedAmount = cash + cashItem.Amount;

    this.formControls.cash.patchValue(addedAmount);

    if (addedAmount - this.totalAmount > 0)
    {
        this.changeAmount = addedAmount - this.totalAmount;
    }

    this.cashConversion();
  }

  getExactAmount(): void
  {
    this.changeAmount = 0;
    this.formControls.cash.patchValue(this.totalAmount.toFixed(2));
  }

  get formControls() { return this.form.controls; }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.totalAmount = this.cashPayDialogData;
    this.form = this.formBuilder.group({
      cash: 0,
    });
    this.cashConversion();
  }

  cashConversion(): void {
    const cash = Number(this.formControls.cash.value);
    if (cash - this.totalAmount > 0)
    {
      this.changeAmount = cash - this.totalAmount;
    } else {
      this.changeAmount = 0;
    }

    const control = this.formControls.cash;
    control.patchValue(Number(control.value).toFixed(2));
  }

  close(): void
  {
    this.DialogRef.close();
  }

  passValuesOnClose(): void
  {
    const checkDetails = this.paymentProcessGenericService.changeCheckDetailsSubject.getValue();

    const paymentRequest = new PaymentRequest();
    paymentRequest.PaymentItemID = checkDetails.CheckUID;
    paymentRequest.PaymentMethodID = TenderTypeID.Cash;
    paymentRequest.Amount = Number(this.form.controls.cash.value);
    paymentRequest.OrderID = checkDetails.OrderSeqID;
    paymentRequest.CashBack = 0.0;

    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(paymentRequest, this.DialogRef).subscribe());
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}

