import { AfterViewInit, Component, ElementRef, EventEmitter, Inject, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { firstValueFrom } from 'rxjs';
import { TenderTypeID } from '../../../@intermediate/models/payment-process/get-process.model';
import { IGiftCardService } from './../../../@intermediate/gift-card.interface';
import { GetGiftCardRequest, SelectedData } from './../../../@intermediate/models/get-gift-card.model';
import { GiftCardReturnFrom, PaymentRequest } from './../../../@intermediate/models/payment-process/post-payment.model';
import { PaymentProcessGenericService } from './../../../@services/payment-process-generic.service';

@Component({
  selector: 'tei-common-payment-processor-gift-card-return',
  templateUrl: './gift-card-return.component.html',
  styleUrls: ['./gift-card-return.component.scss']
})
export class GiftCardReturnComponent implements OnInit, AfterViewInit
{
  @ViewChild('CardNumberInput', { read: ElementRef }) CardNumberInput!: ElementRef;
  @Output() closeEmitter = new EventEmitter<void>();

  form = new FormGroup<GiftCardReturnFrom>({
    totalAmount: new FormControl(this.data.totalAmount, Validators.required),
    cardNumber: new FormControl(null, Validators.required),
    balance: new FormControl(null, Validators.required),
    giftCardUID: new FormControl(null, Validators.required)
  });
  cardIsValid = false;
  currencySymbol: string;
  constructor(
    private dialogRef: MatDialogRef<GiftCardReturnComponent>,
    @Inject('IGiftCardService') private service: IGiftCardService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public manageLoadingService: ManageLoadingService,
    @Inject('IPaymentProcessGenericService') private paymentProcessGenericService: PaymentProcessGenericService,
    public translate: TranslateService,
    private currencyService: TeiCurrencyTranslateService,)
  {
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
  }
  ngAfterViewInit(): void
  {
    setTimeout(() =>
    {
      this.CardNumberInput.nativeElement.focus();
    });
  }

  absAmount(amount: number): number
  {
    return Math.abs(amount);
  }

  onCardNumberBlurred(): void
  {
    this.onLoadBalanceOfCardNumber();
  }
  onCardNumberEntered(): void
  {
    this.CardNumberInput.nativeElement?.blur();
  }


  async onLoadBalanceOfCardNumber(selectedData?: SelectedData): Promise<void>
  {
    this.form.updateValueAndValidity();
    const request = new GetGiftCardRequest();
    request.GiftCardID = +this.form.value?.cardNumber;

    if (!request.GiftCardID)
    {
      return;
    }

    this.cardIsValid = false;
    this.manageLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.service.Get(request));
      if (response.GiftCardUID === '00000000-0000-0000-0000-000000000000')
      {
        this.form.controls.cardNumber.setErrors({ notFound: true });
      } else
      {
        this.cardIsValid = true;
        this.form.controls.cardNumber.setErrors(null);
        this.form.patchValue({
          balance: response.RemainingAmount.toFixed(2),
          giftCardUID: response.GiftCardUID
        });

      }
    }
    catch (error)
    {

    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  onBarcodeScanned(scannedString: string): void
  {
    if (!scannedString)
    {
      return;
    }
    if (this.convertedBarcode(scannedString))
    {

      this.form?.controls.cardNumber.setValue(this.convertedBarcode(scannedString), { emitEvent: false });

    }
    this.onLoadBalanceOfCardNumber();

  }

  convertedBarcode(input: string): string | null
  {
    const teggcIndex = input.indexOf("TEGGC");
    if (teggcIndex === -1)
    {
      return null;
    }

    const firstCaretIndex = input.indexOf("^", teggcIndex);
    if (firstCaretIndex === -1)
    {
      return null;
    }
    const secondCaretIndex = input.indexOf("^", firstCaretIndex + 1);
    if (secondCaretIndex === -1)
    {
      return null;
    }

    return input.substring(firstCaretIndex + 1, secondCaretIndex);
  }

  onlyNumber(event)
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  close(): void
  {
    this.dialogRef.close();
  }



  async ProcessPayment(): Promise<void>
  {
    const request = new PaymentRequest();
    request.PaymentItemID = this.data.checkDetails.CheckUID;
    request.PaymentMethodID = TenderTypeID.GiftCertificate;
    request.ApprovedTransactionID = this.data.checkDetails?.TrxUID;
    request.Amount = this.form.value.totalAmount ? Number(this.form.value.totalAmount) : 0;
    request.IsApproved = true;
    request.OrderID = this.data.checkDetails.OrderSeqID;
    request.ContactID = this.data.checkDetails?.Contact?.ContactID;
    request.PaymentItemID = this.data.checkDetails.CheckUID;
    request.OrderID = this.data.checkDetails.OrderSeqID;
    request.GiftCardID = this.form?.value.giftCardUID;
    this.manageLoadingService.startLoading();

    try
    {
      await firstValueFrom(this.paymentProcessGenericService.ProcessPayment(request, this.dialogRef));
    }
    catch (error)
    {

    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }



}


