import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { AvatarModule, LoadingModule, NotesModule } from '@tei/material';
import { NoteService } from '../../@services/note.service';
import { NoteComponent } from './note.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatInputModule, MatTooltipModule];
const TEI_MATERIAL_MODULES = [NotesModule, AvatarModule, LoadingModule];
const COMPONENTS = [NoteComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES],
  providers: [{ provide: 'INoteService', useClass: NoteService }]
})
export class NoteModule { }
