import { AfterViewInit, ChangeDetectorRef, Component, HostListener, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { SnackBarService } from '@tei/material';
import { Observable, ReplaySubject, Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { ICashEQsService } from '../../@intermediate/cash-eq.interface';
import { IManageContactsPaymentService } from '../../@intermediate/manage-contacts.interface';
import { CashEQsBalanceRequest, CashEQsBalanceResponse } from '../../@intermediate/models/cash-eq-balance.model';
import { TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails } from '../../@intermediate/models/process.model';
import { VirtualCashEQ, VirtualCashEQsRequest, VirtualCashEQsResponse } from '../../@intermediate/models/virtual-cash-eqs.model';
@Component({
  selector: 'tei-common-payment-processor-cash-eq-pay',
  templateUrl: './cash-eq-pay.component.html',
  styleUrls: ['./cash-eq-pay.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(CashEqPayComponent)]
})
export class CashEqPayComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy, AfterViewInit
{
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  checkDetails: CheckDetails;
  barcode = '';
  VirtualCashEQList = new Array<VirtualCashEQ>();
  CashEqItemUID = '';
  invalidClaimCode = false;
  currencySymbol: string;
  claimCodeErrorMessage = '';

  constructor(
    private formBuilder: UntypedFormBuilder,
    private cdRef: ChangeDetectorRef,
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject(MAT_DIALOG_DATA) public cashPayDialogData: number,
    private dialogRef: MatDialogRef<CashEqPayComponent>,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    public manageLoadingService: ManageLoadingService,
    private currencyService: TeiCurrencyTranslateService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    @Inject('IManageContactsPaymentService') private manageContactsService: IManageContactsPaymentService,
    @Inject('ICashEQsService') private cashEQsService: ICashEQsService)
  {
    super(errorStateMatcher);
  }

  @HostListener('document:keyup', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent)
  {
    if (/^[a-z0-9]$/i.test(event.key))
    {
      if (this.barcode.length === 12) { this.barcode = ''; }
      this.barcode += event.key;
      this.formControls.claimCode.setValue(this.barcode);
    }

    if ((event.key === 'NumpadEnter' || event.key === '13' || event.key === 'Enter') && this.barcode.length === 12)
    {
      this.formControls.claimCode.setValue(this.barcode);
      this.formControls.cashEQType.setValue('claimCode');
    }
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.createForm();
  }

  keyup($event: Event): void
  {
    const totalApproved = this.paymentProcessGenericService.ReturnValidValue(this.formControls.totalApproved.value);
    const redeemable = this.paymentProcessGenericService.ReturnValidValue(this.formControls.redeemable.value);
    if (totalApproved > redeemable)
    {
      this.formControls.totalApproved.setValue(redeemable);
    }
    $event.stopPropagation();
    $event.preventDefault();
  }

  claimCodeInputChange($event: Event): void
  {
    $event.stopPropagation();
    $event.preventDefault();
    if (this.formControls.claimCode.value.length === 12)
    {
      this.formControls.cashEQType.setValue('claimCode');
    }
    this.barcode = this.formControls.claimCode.value;
  }

  ngAfterViewInit(): void
  {

    this.changeCheckDetailsEventHandler();
    if (!this.cashPayDialogData)
    {
      return;
    }
    this.form.markAllAsTouched();
  }

  private callValueConversion()
  {
    this.valueConversion('totalApproved');
    this.valueConversion('redeemable');
    this.valueConversion('balance');
  }

  createForm()
  {
    this.form = this.formBuilder.group({
      cashEQType: ['claimCode', Validators.required],
      claimCode: [''],
      balance: [0],
      totalApproved: [0, Validators.required],
      remainingTotal: [0, [Validators.required]],
      redeemable: [0, [Validators.required]],
    });

    this.subscription.add(this.formControls.cashEQType.valueChanges.subscribe(value =>
    {
      this.formControls.balance.setValue(0);
      this.formControls.redeemable.setValue(0);
      if (value === 'claimCode')
      {
        this.refreshClaimCodeBalance();
        return;
      }
      this.barcode = '';
      this.invalidClaimCode = true;
      this.updateContactEQBalance(value);
    }));

    this.subscription.add(this.formControls.claimCode.valueChanges.subscribe(value =>
    {
      if (value !== this.barcode)
      {
        this.invalidClaimCode = true;
      }
      this.validateForm();
    }));
  }

  updateContactEQBalance(CashEQItemID: string)
  {
    this.subscription.add(this.CashEQsBalance('', this.checkDetails?.CheckUID, CashEQItemID).subscribe(value =>
    {
      this.redeemableValidation(value);
    }));
  }

  refreshClaimCodeBalance()
  {
    if (!this.formControls.claimCode.value)
    {
      this.validateAndCallValueConversion();
      return;
    }
    this.invalidClaimCode = false;
    this.barcode = this.formControls.claimCode.value;
    this.subscription.add(this.CashEQsBalance(this.barcode, this.checkDetails?.CheckUID, '').subscribe(value =>
    {
      if (value && value?.Balance > 0)
      {
        this.formControls.balance.setValue(value?.Balance);
      }
      this.redeemableValidation(value);
    }));
  }

  redeemableValidation(value: CashEQsBalanceResponse)
  {
    if (value?.CashEQItemID === '00000000-0000-0000-0000-000000000000' || value.Balance < 0)
    {
      if (this.formControls.cashEQType.value === 'claimCode')
      {
        this.invalidClaimCode = true;
      }
      this.formControls.redeemable.setValue(0);
      this.CashEqItemUID = '';
      this.validateAndCallValueConversion();
      return;
    }
    let redeemable = 0;
    value.RedeemableAmount > value.Balance ? redeemable = value.Balance : redeemable = value.RedeemableAmount;
    this.formControls.redeemable.setValue(redeemable);
    const remainingTotal = this.paymentProcessGenericService.ReturnValidValue(this.formControls.remainingTotal.value);
    remainingTotal < redeemable ? this.formControls.totalApproved.setValue(remainingTotal) : this.formControls.totalApproved.setValue(value.RedeemableAmount);
    this.CashEqItemUID = value.CashEQItemID;
    this.validateAndCallValueConversion();
  }

  private changeCheckDetailsEventHandler()
  {
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(value =>
    {
      if (!value) { return; }
      this.checkDetails = value;

      if (this.checkDetails?.Contact?.ContactID)
      {
        this.subscription.add(this.GetVirtualCashEq(this.checkDetails?.Contact?.ContactID).subscribe(
          {
            next: (GetVirtualCashEqValue) =>
            {
              this.VirtualCashEQList = GetVirtualCashEqValue.VirtualCashEQs;
            }, error: () =>
            {
              this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.CASH_EQ.GET_VIRTUAL_CASH_EQ_ERROR'), '', null, 'left');
            }
          }));
      }
      this.formControls?.remainingTotal?.setValue(this.checkDetails?.Remaining);
      this.formControls?.totalApproved?.setValue(this.checkDetails?.Remaining);
      this.validateAndCallValueConversion();
      this.form.markAllAsTouched();
      this.cdRef.detectChanges();
    }));
  }

  get formControls()
  {
    return this.form?.controls;
  }

  valueConversion(field: string): void
  {
    const control = this.form.get(field);
    control.patchValue(Number(control.value).toFixed(2));
    this.validateForm();
  }

  close(): void
  {
    this.dialogRef.close();
  }

  processPayment(): void
  {
    const request = new PaymentRequest();
    request.PaymentItemID = this.checkDetails.CheckUID;
    request.PaymentMethodID = TenderTypeID.CashEqCertificate;
    request.ContactID = this.checkDetails?.Contact?.ContactID;
    request.Amount = this.paymentProcessGenericService.ReturnValidValue(this.formControls.totalApproved.value);
    request.OrderID = this.checkDetails.OrderSeqID;
    request.CashEqItemUID = this.CashEqItemUID;
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(request, this.dialogRef, false).subscribe());
  }

  validateForm(): boolean
  {
    this.formControls.cashEQType.setErrors(null);
    this.formControls.totalApproved.setErrors(null);
    this.formControls.claimCode.setErrors(null);

    const totalApproved = this.paymentProcessGenericService.ReturnValidValue(this.formControls.totalApproved.value);
    const remainingTotal = this.paymentProcessGenericService.ReturnValidValue(this.formControls.remainingTotal.value);
    const redeemable = this.paymentProcessGenericService.ReturnValidValue(this.formControls.redeemable.value);

    if (!this.formControls.cashEQType.value)
    {
      this.formControls.cashEQType.setErrors({ CASH_EQS_NOT_SELECTED: true });
      return false;
    }

    if (this.formControls.cashEQType.value === 'claimCode')
    {
      if (!this.formControls.claimCode.value)
      {
        this.formControls.claimCode.setErrors({ ENTER_OR_SCAN_CLAIM_CODE_ERROR: true });
        return false;
      }

      if (this.invalidClaimCode || this.formControls.claimCode.value.length < 12)
      {
        this.formControls.claimCode.setErrors({ invalid: true });
        this.claimCodeErrorMessage = this.translate.instant('PAYMENT_PROCESSOR.CASH_EQ.INVALID_CLAIM_CODE_ERROR');
        return false;
      }
    }

    if (this.VirtualCashEQList.find(p => p.CashEQItemUID === this.CashEqItemUID)?.Balance < totalApproved)
    {
      this.formControls.totalApproved.setErrors({ TOTAL_APPROVE_COVER_ERROR: true });
      return false;
    }

    if (redeemable < totalApproved)
    {
      this.formControls.totalApproved.setErrors({ TOTAL_APPROVE_LESS_ERROR: true });
      return false;
    }
    if (totalApproved === 0)
    {
      this.formControls.totalApproved.setErrors({ TOTAL_APPROVE_ZERO_ERROR: true });
      return false;
    }

    if (remainingTotal < totalApproved && !this.paymentProcessGenericService.configuration?.CashEQTenderTypeModel?.AllowTip)
    {
      this.formControls.totalApproved.setErrors({ TOTAL_REMAINING_COVER_ERROR: true });
      return false;
    }

    return true;

  }

  validateAndCallValueConversion(): void
  {
    this.callValueConversion();
    this.validateForm();
  }

  GetVirtualCashEq(ContactID: string): Observable<VirtualCashEQsResponse>
  {
    this.manageLoadingService.startLoading();
    const request = new VirtualCashEQsRequest();
    request.ContactID = ContactID;

    return this.manageContactsService.VirtualCashEQs(request).pipe(
      finalize(() => this.manageLoadingService.finishLoading())
    );
  }

  CashEQsBalance(barcode: string, checkUID: string, CashEQItemID: string): Observable<CashEQsBalanceResponse>
  {
    const subject = new ReplaySubject<CashEQsBalanceResponse>(null);
    this.manageLoadingService.startLoading();
    const request = new CashEQsBalanceRequest();
    request.TrxUID = checkUID;
    request.ContactID = this.checkDetails?.Contact?.ContactID ?? '';

    if (barcode) { request.CodeID = barcode; }
    else { request.CashEQItemID = CashEQItemID; }

    this.subscription.add(this.cashEQsService.Balance(request).pipe(finalize(() =>
    {
      this.manageLoadingService.finishLoading();
      subject.complete();
    })
    ).subscribe({
      next: (result: CashEQsBalanceResponse) =>
      {
        subject.next(result);
      }, error: error =>
      {
        subject.next(null);
        this.claimCodeErrorMessage = error.error.Title;
      }
    }));

    return subject;
  }

  CashEQSelected(): boolean
  {
    if (this.formControls.claimCode.valid)
    {
      return true;
    }

    if (this.CashEqItemUID)
    {
      return true;
    }

    return false;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
