import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IInitializeService } from '../@intermediate/initialize.interface';
import { GetInitializeRequest, GetInitializeResponse } from '../@intermediate/models/initialize.model';

@Injectable({
  providedIn: 'root'
})
export class InitializeService implements IInitializeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/Initialize';
  }

  public Get(request: GetInitializeRequest): Observable<GetInitializeResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetInitializeResponse>(apiUrl, { params: request as any });
  }
}
