import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICancelPaymentService } from '../@intermediate/models/CancelPayment/ICancelPaymentService.interface';
import { PaymentCancelRequest, PaymentCancelResponse } from '../@intermediate/models/CancelPayment/post-cancel-payment.model';

@Injectable({
  providedIn: 'root'
})
export class CancelPaymentService implements ICancelPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/payment/Cancel';
  }

  Post(request: PaymentCancelRequest): Observable<PaymentCancelResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PaymentCancelResponse>(apiUrl, request);
  }
}
