import { Component, EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatStepper } from '@angular/material/stepper';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { TeiCurrencyTranslateService } from "@tei/core-translate";
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IGiftCardService } from './../../../@intermediate/gift-card.interface';
import { GetGiftCardRequest, GetGiftCardResponse, SelectedData } from './../../../@intermediate/models/get-gift-card.model';
import { PaymentsTendered, TenderTypeID } from './../../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from './../../../@intermediate/models/payment-process/post-payment.model';
import { PaymentVoidRequest } from './../../../@intermediate/models/payment-void/post-payment-void.model';
import { CheckDetails } from './../../../@intermediate/models/process.model';
import { PaymentProcessGenericService } from './../../../@services/payment-process-generic.service';

@Component({
  selector: 'tei-common-payment-processor-gift-card-commit',
  templateUrl: './gift-card-commit.component.html',
  styleUrls: ['./gift-card-commit.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(GiftCardCommitComponent)]
})
export class GiftCardCommitComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnChanges, OnDestroy
{
  @Input() stepper: MatStepper;
  @Input() showGratuity: boolean;
  @Input() checkDetails: CheckDetails;
  @Input() selectedData: SelectedData;
  @Input() enableTipsOnGiftCard: boolean;
  @Input() approvedPaymentsTendered: PaymentsTendered;

  @Output() closeEmitter = new EventEmitter<void>();

  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  currencySymbol: string;

  constructor(@SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder,
    private currencyService: TeiCurrencyTranslateService,
    public manageLoadingService: ManageLoadingService,
    @Inject('IGiftCardService') private service: IGiftCardService,
    @Inject('IPaymentProcessGenericService') private paymentProcessGenericService: PaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.form = this.formBuilder.group({
      totalApproved: [this.selectedData?.TotalApproved, Validators.required],
      cardNumber: [this.selectedData?.CardNumber],
      balance: [this.selectedData?.Balance],
      gratuityValue: ['0.00'],
      gratuityPercent: ['0.00'],
      paymentTotal: [this.selectedData?.TotalApproved, Validators.required],
      giftCardUID: ['']
    });

    if (!this.selectedData)
    {
      this.onLoadBalanceOfCardNumber();
    }
  }

  onLoadBalanceOfCardNumber(): void
  {
    const request = new GetGiftCardRequest();
    request.GiftCardID = this.approvedPaymentsTendered?.GiftCardID;

    if (!request.GiftCardID)
    {
      return;
    }

    this.manageLoadingService.startLoading();
    this.subscription.add(this.service.Get(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetGiftCardResponse) =>
      {
        if (response.GiftCardUID === '00000000-0000-0000-0000-000000000000')
        {
          this.formControls.cardNumber.setErrors({ notFound: true });
        } else
        {
          this.selectedData = new SelectedData();

          this.selectedData.Balance = response.RemainingAmount + this.approvedPaymentsTendered?.Amount;
          this.selectedData.CardNumber = request.GiftCardID.toString();
          this.selectedData.TotalApproved = this.approvedPaymentsTendered?.Amount;
          this.selectedData.TransactionID = this.approvedPaymentsTendered?.TrxUID;
          this.selectedData.GiftCardUID = response.GiftCardUID;
          this.setForm();
        }
      }
    }));
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (changes.selectedData && !changes.firstChange)
    {
      if (!this.selectedData)
      {
        this.onLoadBalanceOfCardNumber();
      } else
      {
        this.setForm();
      }
    }
  }

  setForm()
  {
    this.form.patchValue({
      totalApproved: this.selectedData.TotalApproved,
      cardNumber: this.selectedData.CardNumber,
      balance: this.selectedData.Balance,
      paymentTotal: this.selectedData.TotalApproved,
      giftCardUID: this.selectedData?.GiftCardUID
    });
  }

  onlyNumber(event)
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  get formControls()
  {
    return this.form?.controls;
  }

  valueConversion(field: string): void
  {
    const control = this.form.get(field);
    control.patchValue(Number(control.value).toFixed(2));
    control.setErrors(null);
  }

  onGratuityValueInput(): void
  {
    const totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    const gratuityValue = this.returnValidValue(this.formControls?.gratuityValue?.value);
    const percent = totalApproved ? (gratuityValue / totalApproved * 100).toFixed(2) : '0.00';
    this.formControls?.gratuityPercent?.setValue(percent);
    this.formControls?.paymentTotal?.setValue((totalApproved + gratuityValue).toFixed(2));

    this.checkPaymentTotalValidation();
  }

  onTotalApprovedAndGratuityInput(): void
  {
    const totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    const gratuityPercent = this.returnValidValue(this.formControls?.gratuityPercent?.value);
    const gratuityValue = Number((totalApproved * gratuityPercent / 100).toFixed(2));
    this.formControls?.gratuityValue?.setValue(gratuityValue.toFixed(2));
    this.formControls?.paymentTotal?.setValue((totalApproved + gratuityValue).toFixed(2));

    this.checkPaymentTotalValidation();
  }

  returnValidValue(value: string): number
  {
    if (!value)
    {
      value = '0.00';
    }

    return Number(value.toString().replace(/[%]/g, ''));
  }

  private checkPaymentTotalValidation()
  {
    this.formControls?.paymentTotal?.setErrors(null);
    if (this.hasExceedError)
    {
      this.formControls?.paymentTotal?.setErrors({ exceedError: true });
    }
    this.form.markAllAsTouched();
  }

  onCommit(): void
  {
    if (this.form.invalid)
    {
      return;
    }

    if (!this.formControls?.giftCardUID?.value) { return; }

    const request = new PaymentRequest();
    request.PaymentItemID = this.checkDetails.CheckUID;
    request.PaymentMethodID = TenderTypeID.GiftCertificate;
    request.ApprovedTransactionID = this.approvedPaymentsTendered?.TrxUID;
    request.Amount = this.returnValidValue(this.formControls.paymentTotal.value);
    request.TipAmount = this.returnValidValue(this.formControls?.gratuityValue?.value);
    request.GiftCardID = this.formControls?.giftCardUID?.value;
    request.IsApproved = true;
    request.OrderID = this.checkDetails.OrderSeqID;
    this.manageLoadingService.startLoading();
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(request, null, false)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe(result =>
      {
        if (result?.Success)
        {
          this.closeEmitter.emit();
        }
      }));
  }

  onVoid()
  {
    const request = new PaymentVoidRequest();
    request.TrxIDs = [this.selectedData.TransactionID];
    request.PaymentItemID = this.checkDetails?.CheckUID;
    request.PaymentInitiatorUID = this.checkDetails.PaymentRecordUID;
    this.manageLoadingService.startLoading();
    this.subscription.add(this.paymentProcessGenericService.voidPayments(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe(result =>
      {
        if (result)
        {
          this.closeEmitter.emit();
        }
      }));
  }

  get hasExceedError(): boolean
  {
    const totalApproved = this.returnValidValue(this.formControls?.paymentTotal?.value);
    const balance = this.returnValidValue(this.formControls?.balance?.value);

    return totalApproved > balance;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
