import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICashEQsService as ICashEQsService } from '../@intermediate/cash-eq.interface';
import { CashEQsBalanceRequest, CashEQsBalanceResponse } from '../@intermediate/models/cash-eq-balance.model';

@Injectable()
export class CashEQsService implements ICashEQsService
{

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/CashEQs';
  }


  Balance(request: CashEQsBalanceRequest): Observable<CashEQsBalanceResponse>
  {
    const apiUrl = this.serviceUrl + '/Balance';
    return this.http.get<CashEQsBalanceResponse>(apiUrl, { params: request as any });
  }

}
