import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostSendNotificationRequest, PostSendNotificationResponse } from '../@intermediate/models/send-notifications.model';
import { ISendNotificationsService } from '../@intermediate/send-notifications.interface';

@Injectable()
export class SendNotificationsService implements ISendNotificationsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/PaymentNotification';
  }
  public Post(request: PostSendNotificationRequest): Observable<PostSendNotificationResponse>
  {
    const apiUrl = this.serviceUrl + '/SendNotification';
    return this.http.post<PostSendNotificationResponse>(apiUrl, request);
  }
}
