import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { PaymentFinalizeRequest } from '../../@intermediate/models/payment-finalize.model';
import { PaymentProcessState } from '../../@intermediate/models/process.model';
import { PaymentFinalizeService } from '../../@services/payment-finalize.service';
import { PaymentNavigationService } from '../../@services/payment-navigation.service';

@Component({
  selector: 'tei-common-payment-processor-finalize',
  templateUrl: './finalize.component.html',
  styleUrls: ['./finalize.component.scss']
})
export class FinalizeComponent implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  paymentUID: PaymentFinalizeRequest = new PaymentFinalizeRequest();
  constructor(
    private paymentNavigationService: PaymentNavigationService,
    private route: ActivatedRoute,
    @Inject('IPaymentFinalizeService') private finalizeService: PaymentFinalizeService,
  ) { }

  ngOnInit()
  {
    const stateId = this.route.snapshot.queryParamMap.get('stateId') as PaymentProcessState;
    this.load();
    this.detectRouteToNavigate(stateId);
  }

  load()
  {
    this.subscription.add(this.finalizeService.PostFinalize(this.paymentUID).subscribe());
  }

  detectRouteToNavigate(stateId: PaymentProcessState): void
  {

    // ToDO :
    // what we need : finalize api to return back us some data (with route link) in load() function.
    // what we expect : the route that we need to redirect on , should came from backend .
    // for now we are creating that in frontend side .
    switch (stateId)
    {
      case PaymentProcessState.Success: {
        this.paymentNavigationService.success();
        break;
      }

      // TODO : OrderId should return from backend
      case PaymentProcessState.CancelByUser: {
        this.paymentNavigationService.cancelByUser();
        break;
      }
      case PaymentProcessState.FailedOnInitialize: {
        this.paymentNavigationService.failedOnInitialize();
        break;
      }
    }
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
