import { Component, Input, OnInit } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';
import { ManageLoadingService } from '@tei/core';
import { TeiCurrencyTranslateService } from "@tei/core-translate";
import { CardTypeEnum } from '../../../@intermediate/enums/credit-debit.enum';
import { CreditDebitDialog } from '../../../@intermediate/models/credit-debit-model';

@Component({
  selector: 'tei-common-payment-processor-integrated',
  templateUrl: './integrated.component.html',
  styleUrls: ['./integrated.component.scss'],
})
export class IntegratedComponent implements OnInit
{
  @Input() creditDebitData: CreditDebitDialog;
  @Input() totalApproved: UntypedFormControl;
  @Input() paymentMassager: boolean;

  newCardSelected = true;
  currencySymbol: string;
  cards: MockCardModel[]; // todo: add type after API becomes ready
  selectedCard = new MockCardModel(); // todo: add type after API becomes ready

  get cardTypeEnum(): typeof CardTypeEnum
  {
    return CardTypeEnum;
  }

  constructor(private currencyService: TeiCurrencyTranslateService
    , public manageLoadingService: ManageLoadingService)
  {
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.valueConversion();
    this.mockData();
  }

  valueConversion()
  {
    const price = Number(this.totalApproved.value);
    this.totalApproved.patchValue(price.toFixed(2));
  }

  onTotalApprovedValue(value: string)
  {
    this.totalApproved.setValue(value);
  }

  onSelectCard(value?: string)
  {
    if (value)
    {
      this.selectedCard.Id = value;
      this.newCardSelected = false;
    }
    else
    {
      this.selectedCard.Id = null;
      this.newCardSelected = true;
    }
  }

  setImageName(value: number)
  {
    switch (value)
    {
      case this.cardTypeEnum.MasterCard:
        return 'MasterCard.svg';
      case this.cardTypeEnum.JCB:
        return 'JCS.svg';
      case this.cardTypeEnum.Visa:
        return 'Visa.svg';
      case this.cardTypeEnum.AMEX:
        return 'Amex.svg';
      case this.cardTypeEnum.DinersClub:
        return 'Diners.svg';
      case this.cardTypeEnum.Discover:
        return 'Discover.svg';
      case this.cardTypeEnum.Discover:
        return 'UnionPay.svg';
      default:
        return 'DefaultCard.svg';
    }
  }

  mockData()
  {
    this.cards = [{
      Id: '0',
      cardType: this.cardTypeEnum.Visa,
      Code: '0707',
      ExpireDate: '05/22'
    },
    {
      Id: '2',
      cardType: this.cardTypeEnum.MasterCard,
      Code: '0001',
      ExpireDate: '05/22'
    }];
  }



}

class MockCardModel
{
  Id: string;
  cardType: CardTypeEnum;
  Code: string;
  ExpireDate: string;
}
