import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';

export class VoidPaymentDialogRequest
{
  PaymentInitiatorUID: string;
  PaymentItemID: string;
  TrxIDs: Array<string>;
  PaymentUID: string;
}

export class VoidPaymentDialogRequestForm extends UntypedFormGroup
{
  constructor(PaymentInitiatorUID: string, PaymentItemID: string, TrxIDs: Array<string>, PaymentUID: string)
  {
    super({
      PaymentInitiatorUID: new UntypedFormControl(PaymentInitiatorUID, []),
      PaymentItemID: new UntypedFormControl(PaymentItemID, []),
      TrxIDs: new UntypedFormControl(TrxIDs, []),
      PaymentUID: new UntypedFormControl(PaymentUID, []),
    });
  }

  public value: VoidPaymentDialogRequest;

  public setValue(
    value: VoidPaymentDialogRequest,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    super.setValue(value, options);
  }
}
