import { Component, Input } from '@angular/core';
import { ItemPaymentSummary } from '../../@intermediate/models/payment-process/get-process.model';

@Component({
  selector: 'tei-check-fees',
  templateUrl: './check-fees.component.html',
  styleUrls: ['./check-fees.component.scss']
})
export class CheckFeesComponent
{
  @Input() checkMenuItem: ItemPaymentSummary;
  @Input() remainingTotal: number;
  @Input() gratuity: { AutoGratuityDisplay: string, EnableAutoGratuity: boolean; };

  absAmount(amount: number): number
  {
    return Math.abs(amount);
  }
}
