import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IAccountService } from '../@intermediate/account.interface';
import { GetAccountsRequest, GetAccountsResponse } from '../@intermediate/models/get-account.model';

@Injectable({
  providedIn: 'root'
})
export class AccountService implements IAccountService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Office/Contacts/Accounts';
  }

  public Get(request: GetAccountsRequest): Observable<GetAccountsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAccountsResponse>(apiUrl, { params: request as any });
  }
}
