export class GetAccountsRequest
{
  ContactID: string;
}

export class GetAccountsResponse
{
  Result: boolean;
  Accounts: Array<Account>;
}

export class Account
{
  AccountID: string;
  AccountName: string;
  MemberID: string;
  isDefault: boolean;
  AllowAR: boolean;
  CreditLimitType: CreditLimitType;
  CreditLimitAmount: number;
}

export enum CreditLimitType
{
  NoCredit,
  Unlimited,
  Amount
}
