import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, HostListener, Inject, OnDestroy, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService, StorageService } from '@tei/core';
import { TeiCurrencyTranslateService } from "@tei/core-translate";
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { ContactQuickSearchComponent } from '@tei/office-contacts';
import { EMPTY, Observable, Subscription, firstValueFrom } from 'rxjs';
import { catchError, finalize } from 'rxjs/operators';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { ICheckContactService } from '../../@intermediate/check-contact.interface';
import { PostPaymentPrintRequest } from '../../@intermediate/models/Print/post-payment-print-request.model';
import { PostPaymentPrintResponse } from '../../@intermediate/models/Print/post-payment-print-response.model';
import { AddContactToCheckRequest } from '../../@intermediate/models/add-contact-to-check.model';
import { GetRedeemablePointsRequest, GetRedeemablePointsResponse } from '../../@intermediate/models/get-redeemable-points.model';
import { PaymentInitiator, REFERENCE_KEY_NAME, ReferenceModel } from '../../@intermediate/models/initialize.model';
import { PrintReceiptOption } from '../../@intermediate/models/payment-configuration.model';
import { PaymentFinalizeRequest, PaymentFinalizeResponse } from '../../@intermediate/models/payment-finalize.model';
import { Contact, GetProcessRequest, GetProcessResponse, ItemStatus, Note, PaymentItem, PaymentProcess, PaymentStatus, PaymentsTenderedStatus, TenderTypeID, TransactionType } from '../../@intermediate/models/payment-process/get-process.model';
import { OrderStatus, PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails, PaymentButtonsStatus, PaymentProcessState, ReceiptModel, ReceiptType } from '../../@intermediate/models/process.model';
import { ContactInfo, PostSendNotificationRequest } from '../../@intermediate/models/send-notifications.model';
import { IPointsService } from '../../@intermediate/points.interface';
import { IPrintService } from '../../@intermediate/print.interface';
import { IProcessService } from '../../@intermediate/process.interface';
import { ISendNotificationsService } from '../../@intermediate/send-notifications.interface';
import { PaymentFinalizeService } from '../../@services/payment-finalize.service';
import { PaymentNavigationService } from '../../@services/payment-navigation.service';
import { NoteComponent } from '../../components/note/note.component';
import { VoidPaymentComponent } from '../../components/void-payment/void-payment.component';

@Component({
  selector: 'tei-common-payment-processor-process',
  templateUrl: './process.component.html',
  styleUrls: ['./process.component.scss']
})
export class ProcessComponent implements OnInit, OnDestroy, AfterViewInit
{
  @ViewChild('teiContactQuickSearch') teiContactQuickSearch: ContactQuickSearchComponent;
  @ViewChildren('checkTabItemContainer') checkTabItemContainer: QueryList<HTMLElement>;
  @ViewChildren('noteText') noteElements!: QueryList<ElementRef>;
  checkTabItemContainerHTMLElement: QueryList<ElementRef>;
  isOverflowing: boolean[] = [];
  checkDetails = new CheckDetails();
  showSummary = true;
  showReceipt = true;
  paymentRecordId: string;
  paymentRecordUID: string;
  paymentDetail: PaymentProcess;
  selectedCheckPanel: PaymentItem;
  paidItem: PaymentItem[] = [];
  unPaidItem: PaymentItem[] = [];
  innerWidth: number;
  drawerBackDropMode: string = 'side' || 'over';
  viewMode: string = 'desktop' || 'mobile';
  selectedCheckIndex: number;
  processRequest: GetProcessRequest = new GetProcessRequest();
  paymentInitiator = PaymentInitiator;
  title: string;
  showGratuity: boolean;
  isSingleCheck: boolean;
  selectedContact: Contact;
  cash: number;
  paymentButtonsStatus = new PaymentButtonsStatus();
  hasPartialPayment: boolean;
  contactPoints: GetRedeemablePointsResponse;
  currencySymbol: string;
  initiator: PaymentInitiator;
  subscription: Subscription = new Subscription();
  responsiveServiceSubscription: Subscription;
  screenSize = '';
  TransactionType: TransactionType;
  paymentMethodTitle: string;
  get needPayment(): boolean
  {
    return !this.paymentButtonsStatus.PaymentCompleted || this.paymentButtonsStatus.PaymentRequired;
  }

  get windowIsGreaterThanMedium(): boolean { return this.screenSize === 'lg' || this.screenSize === 'xl' || this.screenSize === 'xxl'; }
  get mobileMode(): boolean { return (this.innerWidth <= 767.98 || (this.screenSize === 'xs' || this.screenSize === 'sm')); }
  get tabletMode(): boolean { return (this.innerWidth > 768 && this.innerWidth < 991.98) || (this.screenSize === 'md'); }
  get desktopMode(): boolean { return (this.innerWidth > 992) || (this.screenSize !== 'xs' && this.screenSize !== 'sm' && this.screenSize !== 'md'); }

  @HostListener('window:beforeunload', ['$event'])
  Onbeforeunload(event: BeforeUnloadEvent): void
  {
    if (!this.paymentButtonsStatus.CanReload)
    {
      event.preventDefault();
      event.returnValue = '';
    } else
    {
      delete event.returnValue;
    }
  }

  constructor(
    private route: ActivatedRoute,
    @Inject('IProcessService') private paymentProcessService: IProcessService,
    @Inject('ICheckContactService') private checkContactService: ICheckContactService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService,
    private paymentNavigationService: PaymentNavigationService,
    private changeDetectorRef: ChangeDetectorRef,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    private storageService: StorageService,
    private dialog: MatDialog,
    @Inject('IPointsService') private pointsService: IPointsService,
    @Inject('IPrintService') private printService: IPrintService,
    @Inject('ISendNotificationsService') private sendNotificationService: ISendNotificationsService,
    @Inject('IPaymentFinalizeService') private finalizeService: PaymentFinalizeService,
    private responsiveService: ResponsiveService,
    private currencyService: TeiCurrencyTranslateService
  ) { }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.paymentRecordUID = this.route.snapshot.paramMap.get('PaymentRecordUID');
    this.initiator = this.storageService.Get('initiator');
    this.innerWidth = window.innerWidth;

    if (innerWidth > 425)
    {
      this.drawerBackDropMode = 'side';
      this.viewMode = 'desktop';
    } else
    {
      this.drawerBackDropMode = 'over';
      this.viewMode = 'mobile';
    }

    this.responsiveServiceSubscription = this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.innerWidth = window.innerWidth;


      if (innerWidth > 992)
      {
        this.showReceipt = true;
        this.showSummary = true;
      } else if (this.screenSize !== screenSize)
      {
        this.showReceipt = true;
        this.showSummary = false;
      }
      this.screenSize = screenSize;

    });

    this.load();

    this.subscription.add(this.paymentProcessGenericService.dialogUpdateMessageEvent.subscribe(
      () =>
      {
        this.paymentAppliedEventHandler();
      }
    ));
  }

  async openNoteDialog(note: Note, itemID: string): Promise<void>
  {
    const noteModal = this.dialog.open(NoteComponent, {
      width: '400px',
      panelClass: ['a-note-modal', 'o-dialog-sm-fullscreen'],
      data: {
        Note: note,
        ItemID: itemID
      }
    });

    try
    {
      const result = await firstValueFrom(noteModal.afterClosed());
      note = result.newNote;
      this.updateTooltip();
    } catch (error)
    {
      console.log(error);
    }
  }
  public get paymentProcessState(): typeof PaymentProcessState
  {
    return PaymentProcessState;
  }

  get isMobile(): boolean
  {
    return this.viewMode === 'mobile';
  }

  get isDesktop(): boolean
  {
    return this.viewMode === 'desktop';
  }

  get paymentStatus(): typeof PaymentStatus
  {
    return PaymentStatus;
  }

  load(): void
  {
    this.paymentRecordId = (this.storageService.Get(REFERENCE_KEY_NAME) as ReferenceModel)?.id;
    this.processRequest.RefID = this.paymentRecordId;
    this.manageLoadingService.startLoading();

    this.subscription.add(
      this.paymentProcessService.Get(this.processRequest)
        .pipe(
          finalize(() =>
          {
            this.manageLoadingService.finishLoading();
          }),
          catchError((errMessage) =>
          {
            this.paymentButtonsStatus.Back = true;
            this.isSingleCheck = true;
            return EMPTY;
          })
        )
        .subscribe(
          (paymentDetail: GetProcessResponse) =>
          {
            this.showInCompleteTransactionDialogue(paymentDetail);
            this.loadItems(paymentDetail);
            this.updateTooltip();
            this.updatePaymentMethodsTitle(paymentDetail.Payment.IsReturn);
          }
        )
    );

  }

  showInCompleteTransactionDialogue(paymentDetail: GetProcessResponse): void
  {

    const incompletePayment = paymentDetail.Payment.Items.some(item => item.Status === PaymentStatus.PartiallyPaid);
    if (this.paymentProcessGenericService.inCompleteTransactionDialogue || incompletePayment)
    {
      const dialogRef = this.dialog.open(CardDialogComponent, {
        panelClass: ['move-item-dialog-wrapper', 'o-dialog-md-fullscreen'],
        data: {
          component: ConfirmationDialogComponent,
          dialogData: {
            title: 'PAYMENT_PROCESSOR.PROCESS.INCOMPLETE_TRANSACTION_DIALOG_TITLE',
            content: this.translate.instant('PAYMENT_PROCESSOR.PROCESS.INCOMPLETE_TRANSACTION_DIALOG_DESCRIPTION'),
            closeButtonLabel: 'PAYMENT_PROCESSOR.PROCESS.CLOSE'
          }
        },
        width: '350px',
      });

      this.subscription.add(dialogRef.afterClosed().subscribe(() => { }));
    }
  }

  getContactPoints(): void
  {
    const request: GetRedeemablePointsRequest = {
      ContactID: this.selectedContact?.ContactID,
      POSTrxUID: this.selectedCheckPanel?.ItemID,
    };
    if (this.selectedContact?.ContactID)
    {
      this.subscription.add(this.pointsService.GetRedeemablePoints(request).subscribe({
        next: (response) =>
        {
          this.contactPoints = response;
        }
      }
      ));
    }
  }

  private loadItems(paymentDetail: GetProcessResponse): void
  {
    this.paymentProcessGenericService.configuration = paymentDetail.Payment.Configuration;
    this.paymentDetail = paymentDetail.Payment;
    this.paidItem = this.paymentDetail.Items.filter(item => item.Status === PaymentStatus.Paid);
    this.unPaidItem = this.paymentDetail.Items.filter(item => item.Status !== PaymentStatus.Paid);
    this.paymentDetail.Items = this.unPaidItem.concat(this.paidItem);
    this.hasPartialPayment = this.paymentDetail.Items.findIndex(item => item.Status === PaymentStatus.PartiallyPaid) >= 0;
    this.title = paymentDetail.Payment.Title;
    this.showGratuity = paymentDetail.Payment.ShowGratuity;
    this.isSingleCheck = paymentDetail.Payment.IsSingleCheck;
    this.selectFirstCheck();
    this.updateCheckDetails();
    this.changeDetectorRef.detectChanges();
  }

  selectCheck(check: PaymentItem): void
  {
    this.selectedContact = check.PaymentContact;
    this.selectedCheckPanel = check;
    this.checkDetails.CheckErrorMessage = this.selectedCheckPanel.ErrorMessage;

    this.detectCurrentIndexOfChecksList();
    this.updateCheckDetails();
    this.handlePartialPaymentStatus();
    this.showPromotionDialogToDoneCheck();
  }

  showPromotionDialogToDoneCheck(): void
  {
    if (this.selectedCheckPanel.Remaining !== 0 || this.selectedCheckPanel.Status !== PaymentStatus.Unpaid)
    {
      return;
    }

    if (this.selectedCheckPanel.Remaining === 0 && this.isSingleCheck)
    {
      this.payCash();
      return;
    }

    const title = this.selectedCheckPanel.Title ?
      this.translate.instant('PAYMENT_PROCESSOR.PROCESS.CHECK') + ' #' + this.selectedCheckPanel.Title
      : this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ORDER');

    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: ['move-item-dialog-wrapper', 'o-dialog-md-fullscreen'],
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'PAYMENT_PROCESSOR.PROCESS.PROMOTION_DIALOG_TITLE',
          content: this.isSingleCheck ? this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PROMOTION_DIALOG_DESCRIPTION_WITHOUT_CHECK', { checkNumber: title, currencySymbol: this.currencySymbol }) :
            this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PROMOTION_DIALOG_DESCRIPTION_CHECK', { checkNumber: title, currencySymbol: this.currencySymbol }),
          submitButtonLabel: 'PAYMENT_PROCESSOR.PROCESS.MARK_DONE',
          submitButtonClass: 'mat-accent',
          cancelButtonLabel: 'PAYMENT_PROCESSOR.PROCESS.CANCEL',
        }
      },
      width: '350px',
    });

    this.subscription.add(dialogRef.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.payCash();
      }
    }));
  }

  payCash(): void
  {
    const paymentRequest = new PaymentRequest();
    paymentRequest.PaymentItemID = this.checkDetails.CheckUID;
    paymentRequest.PaymentMethodID = TenderTypeID.Cash;
    paymentRequest.Amount = 0;
    paymentRequest.OrderID = this.checkDetails.OrderSeqID;
    paymentRequest.CashBack = 0.0;

    this.manageLoadingService.startLoading();
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(paymentRequest, null)
      .pipe(finalize(() => { this.manageLoadingService.finishLoading(); }))
      .subscribe());
  }


  updateCheckDetails(): void
  {
    this.checkDetails.Contact = this.selectedContact;
    this.checkDetails.Remaining = this.selectedCheckPanel?.Remaining;
    this.checkDetails.IsSentToKitchen = this.selectedCheckPanel?.Summary?.Items?.every(p => p.Status === ItemStatus.SentToKitchen);
    this.checkDetails.PaymentsTendered = this.selectedCheckPanel?.Summary?.PaymentSummary?.PaymentsTendered;
    this.checkDetails.CheckUID = this.selectedCheckPanel?.ItemID;
    this.checkDetails.Title = this.selectedCheckPanel?.Title;
    this.checkDetails.OrderSeqID = this.paymentRecordId;
    this.checkDetails.Status = this.selectedCheckPanel.Status;
    this.checkDetails.Total = this.selectedCheckPanel?.Summary?.PaymentSummary?.Total;
    this.checkDetails.initiator = this.initiator;
    this.checkDetails.PaymentRecordUID = this.paymentRecordUID;
    this.checkDetails.TerminalTenderType = this.paymentDetail.TenderType;
    this.checkDetails.isSingleCheck = this.paymentDetail.IsSingleCheck;
    this.checkDetails.PaymentItem = this.paymentDetail?.Items;
    this.paymentProcessGenericService.changeCheckDetailsSubject.next(this.checkDetails);
    this.getContactPoints();
  }

  detectCurrentIndexOfChecksList(): void
  {
    this.selectedCheckIndex = 1;
    if (this.selectedCheckPanel?.ItemID)
    {
      this.selectedCheckIndex = this.paymentDetail.Items.findIndex(item => item?.ItemID === this.selectedCheckPanel?.ItemID) + 1;
    }
  }

  selectNextCheck(): void
  {
    if (this.isSingleCheck || this.paymentDetail.Items.length === 1)
    {
      const errorMessage = this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NO_ADDITIONAL_CHECKS_ERROR');
      this.snackBarService.showError(errorMessage, '', null, 'left');
      return;
    }

    const tabItem = (this.selectedCheckIndex >= this.paymentDetail.Items.length) ?
      this.paymentDetail.Items[0] : this.paymentDetail.Items[this.selectedCheckIndex];

    const lastCheck = this.selectedCheckIndex;
    this.selectCheck(tabItem);
    if (this.innerWidth <= 991.98)
    {
      if (this.innerWidth > 767 && this.selectedCheckIndex < 4)
      {
        if (this.selectedCheckIndex !== 1 || lastCheck === 1)
        {
          return;
        }
      }

      const check = this.checkTabItemContainerHTMLElement?.find(p => p?.nativeElement?.id === `checkTabItem_${ tabItem.ItemID }`);

      check?.nativeElement?.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
        inline: 'center'
      });
    }
  }

  selectFirstCheck(): void
  {
    this.detectCurrentIndexOfChecksList();

    if (this.selectedCheckIndex > 0)
    {
      this.selectedCheckPanel = this.paymentDetail?.Items[this.selectedCheckIndex - 1];

    } else if (this.unPaidItem.length > 0)
    {
      this.selectedCheckIndex = 1;
      this.selectedCheckPanel = this.unPaidItem[0];
    } else
    {
      this.selectedCheckIndex = 1;
      this.selectedCheckPanel = this.paidItem[0];
    }

    this.checkDetails.CheckErrorMessage = this.selectedCheckPanel.ErrorMessage;
    this.selectedContact = this.selectedCheckPanel.PaymentContact;
    this.handlePartialPaymentStatus();
    this.updateCheckDetails();
    this.showPromotionDialogToDoneCheck();
    this.changeDetectorRef.detectChanges();
  }

  handlePartialPaymentStatus(): void
  {
    if (this.selectedCheckPanel.Remaining > 0 && this.selectedCheckPanel.Remaining < this.selectedCheckPanel.Summary.PaymentSummary.Total)
    {
      this.selectedCheckPanel.Status = this.paymentStatus.PartiallyPaid;
    }

    switch (this.selectedCheckPanel.Status)
    {
      case PaymentStatus.Paid:
        this.paymentButtonsStatus = this.paymentDetail.Configuration.PaymentButtonsStatus.Paid;
        break;
      case PaymentStatus.PartiallyPaid:
        this.paymentButtonsStatus = this.paymentDetail.Configuration.PaymentButtonsStatus.PartiallyPaid;
        break;
      case PaymentStatus.Unpaid:
        this.paymentButtonsStatus = this.paymentDetail.Configuration.PaymentButtonsStatus.Unpaid;
        break;
    }
    this.paymentButtonsStatus.Void = this.hasPaidPayment();
    this.changeDetectorRef.detectChanges();

  }

  onContactItemClickEvent(contact: Contact)
  {
    const errMessage = this.validateChangeContact();
    if (errMessage)
    {
      this.snackBarService.showError(errMessage, '', null, 'left');
      return;
    }
    this.selectedContact = contact;
    const request = new AddContactToCheckRequest();
    request.ItemID = this.selectedCheckPanel?.ItemID;
    request.ContactID = contact?.ContactID;
    if (this.isSingleCheck)
    {
      request.RefID = this.paymentRecordId;
    }
    request.AccountID = '';

    this.manageLoadingService.startLoading();
    this.subscription.add(this.checkContactService.AddContactToCheck(request)
      .pipe(finalize(() => { this.manageLoadingService.finishLoading(); }))
      .subscribe({
        next: () =>
        {
          this.refreshChecks();
        }
      }));
  }

  removeContactItemClickEvent()
  {
    const errMessage = this.validateChangeContact();

    if (errMessage)
    {
      this.snackBarService.showError(errMessage, '', null, 'left');
      return;
    }

    this.selectedContact = null;

    const request = new AddContactToCheckRequest();
    request.ItemID = this.selectedCheckPanel?.ItemID;
    request.ContactID = null;
    if (this.isSingleCheck)
    {
      request.RefID = this.paymentRecordId;
    }
    request.AccountID = null;

    this.manageLoadingService.startLoading();
    this.subscription.add(this.checkContactService.DeleteContactFromCheck(request)
      .pipe(finalize(() => { this.manageLoadingService.finishLoading(); }))
      .subscribe({
        next: () =>
        {
          this.refreshChecks();
        }
      }));
  }

  validateChangeContact(): string
  {

    if (this.checkDetails?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.MemberAccount && p.Status === PaymentsTenderedStatus.Approved))
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.HAVE_UNCOMMITTED_ON_ACCOUNT');
    } else if (this.hasPaidContact() || this.hasPartiallyPaidPayment())
    {
      return this.translate.instant('PAYMENT_PROCESSOR.PROCESS.ALREADY_PAID_ERROR', { title: this.checkDetails.Title ?? '-' });
    }
    return null;
  }

  refreshChecks()
  {
    this.manageLoadingService.startLoading();
    this.subscription.add(this.paymentProcessService.Get(this.processRequest).pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((paymentDetail: GetProcessResponse) =>
      {
        if (paymentDetail?.Payment.Items)
        {
          this.handlePartialPaymentStatus();
          this.loadItems(paymentDetail);
          this.selectedCheckPanel = this.paymentDetail.Items[this.selectedCheckIndex - 1];
          this.checkDetails.CheckErrorMessage = this.selectedCheckPanel.ErrorMessage;

        }
      }));
  }

  onShowSummary()
  {
    this.showSummary = !this.showSummary;
  }

  openContactDialog(): void
  {
    this.teiContactQuickSearch.openQuickSearch();
  }

  async done(): Promise<void>
  {

    const recipientModel: ReceiptModel = {
      Type: ReceiptType.Print,
      CopyCount: 1,
      PrinterID: this.paymentProcessGenericService.selectedPrinterID
    } as ReceiptModel;

    if (this.paymentDetail.Configuration?.NeedFinalizeOrder)
    {
      if (this.hasPartialPayment)
      {
        const requiredMessage = this.translate.instant('PAYMENT_PROCESSOR.PROCESS.MUST_COMPLETE_PAYMENT_ERROR');
        this.snackBarService.showError(requiredMessage, '', null, 'left');
        return;
      }
      this.manageLoadingService.startLoading();
      const finalizeResult = (await this.finalizeOrder(recipientModel, [PrintReceiptOption.PackageReceipt, PrintReceiptOption.SlipReceipt])).pipe(finalize(() => { this.manageLoadingService.finishLoading(); }));
      if (finalizeResult)
      {
        const finalizeResponse = await firstValueFrom(finalizeResult);
        if (!!finalizeResponse?.PrintUrls?.length)
        {
          this.downloadPrintedFiles(finalizeResponse);
        }
      }
    }
    else if (this.paymentDetail.Configuration?.PrintReceiptOptions?.some(a => a === PrintReceiptOption.PackageReceipt))
    {

      const printResult$ = this.printReceipt(recipientModel, [PrintReceiptOption.PackageReceipt]);
      if (printResult$)
      {
        const printResponse = await firstValueFrom(printResult$);
        if (!!printResponse?.PrintUrls?.length)
        {
          this.downloadPrintedFiles(printResponse);
        }
      }

    }


    this.navigateToMainPage();
  }

  private async finalizeOrder(recipientModel: ReceiptModel, printOptions: PrintReceiptOption[]): Promise<Observable<PaymentFinalizeResponse>>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const postPaymentPrintRequest: PostPaymentPrintRequest = {
        RefID: this.paymentRecordId,
        CheckUIDs: [this.checkDetails?.CheckUID],
        PrinterID: recipientModel.PrinterID || null,
        Quantity: recipientModel.CopyCount,
        PrintConfigurations: recipientModel.PrintConfigurations || [],
        PaymentRecordUID: this.paymentRecordUID,
        PrintReceiptOptions: printOptions,
        HideTipLine: true,
        ShowSignatureSegment: true
      };
      const request: PaymentFinalizeRequest = {
        OrderId: this.paymentDetail.OrderID,
        PaymentItemID: this.checkDetails?.CheckUID,
        PaymentPrintRequest:
          this.paymentDetail.Configuration?.PrintReceiptOptions?.some(a => a === PrintReceiptOption.PackageReceipt || a === PrintReceiptOption.SlipReceipt) ?
            postPaymentPrintRequest : null
      };

      const response = await this.finalizeService.PostFinalize(request);
      return response;
    }
    catch (error)
    {
      console.error("Error finalizing order:", error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  private navigateToMainPage()
  {
    if (this.unPaidItem.length || this.hasPartialPayment || !this.paymentDetail || this.paymentDetail.OrderStatus === OrderStatus.Reactivated)
    {
      this.paymentNavigationService.cancelByUser();
    }
    else
    {
      this.paymentNavigationService.success();
    }
  }

  cancelPayment(): void
  {
    if (!this.paymentButtonsStatus.Back)
    {
      if (this.paymentDetail.Configuration.NeedFinalizeOrder)
      {
        const requiredMessage = this.translate.instant('PAYMENT_PROCESSOR.PROCESS.MUST_COMPLETE_PAYMENT_ERROR');
        this.snackBarService.showError(requiredMessage, '', null, 'left');
      }
    }
    else
    {
      this.navigateToMainPage();
    }
  }

  paymentAppliedEventHandler(): void
  {
    this.refreshChecks();
  }

  hasPaidContact(): boolean
  {
    return this.checkDetails?.PaymentsTendered?.some(p => p.Status !== PaymentsTenderedStatus.Voided);
  }

  get hasOnAccountPayment(): boolean
  {
    return this.selectedCheckPanel?.Summary?.PaymentSummary?.PaymentsTendered?.some(p => p.TenderTypeID === TenderTypeID.MemberAccount
      && p.Status !== PaymentsTenderedStatus.Voided);
  }

  hasPaidPayment(): boolean
  {
    return this.selectedCheckPanel?.Summary?.PaymentSummary?.PaymentsTendered?.some(p => p.Status !== PaymentsTenderedStatus.Voided);
  }

  hasPartiallyPaidPayment(): boolean
  {
    return this.selectedCheckPanel?.Status === this.paymentStatus.PartiallyPaid;
  }

  voidPayment(): void
  {
    const dialog = this.dialog.open(VoidPaymentComponent, {
      panelClass: ['void-payment-dialog', 'o-dialog-md-fullscreen'],
      width: '400px',
      data: {
        checkDetails: this.checkDetails,
        PaymentMethods: this.paymentDetail?.TenderType?.PaymentMethods
      }
    });

    if (dialog)
    {
      this.subscription.add(dialog.backdropClick().subscribe(() =>
      {
        dialog.disableClose = this.manageLoadingService.isLoading;
        return !dialog.disableClose;
      }));
    }
  }

  printReceipt(recipientModel: ReceiptModel, printOptions: Array<PrintReceiptOption>): Observable<PostPaymentPrintResponse>
  {
    if (recipientModel.Type === ReceiptType.Print)
    {
      return this.printOrder(recipientModel, printOptions);
    }
    if (recipientModel.Type === ReceiptType.Email)
    {
      this.sendReceiptByEmail(recipientModel);
      return null;
    }
  }

  private printOrder(recipientModel: ReceiptModel, printOptions: PrintReceiptOption[]): Observable<PostPaymentPrintResponse>
  {
    const request = new PostPaymentPrintRequest();
    request.RefID = this.paymentRecordId;
    request.CheckUIDs = [this.checkDetails?.CheckUID];
    request.PrinterID = recipientModel.PrinterID || null;
    request.Quantity = recipientModel.CopyCount;
    request.PrintConfigurations = recipientModel.PrintConfigurations || null;
    request.PaymentRecordUID = this.paymentRecordUID;
    request.PrintReceiptOptions = printOptions;
    request.HideTipLine = true;
    this.manageLoadingService.startLoading();
    return this.printService.Post(request)
      .pipe(finalize(() => { this.manageLoadingService.finishLoading(); }));
  }

  private downloadPrintedFiles(response: PostPaymentPrintResponse)
  {
    if (!!response?.PrintUrls?.length)
    {
      for (let index = 0; index < response.PrintUrls.length; index++)
      {
        const element = response.PrintUrls[index].PrintToFileUrl;
        this.paymentProcessGenericService.DownloadFile(element, 'Order_' + this.selectedCheckPanel.ItemID + '_' + index);
      }
    }
    else
    {
      this.snackBarService.showSuccess(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PRINT_STARTED'), '', null, 'left');
    }
  }

  async receiptActionHandler(recipientModel: ReceiptModel): Promise<void>
  {
    if (!this.paymentButtonsStatus.PaymentCompleted || this.paymentButtonsStatus.PaymentRequired)
    {
      this.snackBarService.showError(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.NOT_PAID_ERROR',
        { checkTitle: this.checkDetails.OrderSeqID }), '', null, 'left');
      return;
    }

    const printResponse = await firstValueFrom(this.printReceipt(recipientModel, [
      PrintReceiptOption.OrderReceipt,
      PrintReceiptOption.GiftCardReceipt,
    ]));
    this.downloadPrintedFiles(printResponse);
  }

  sendReceiptByEmail(recipientModel: ReceiptModel): void
  {
    const sendNotificationRequest: PostSendNotificationRequest = {
      CheckUID: this.checkDetails?.CheckUID,
      PaymentRecordUID: this.paymentRecordUID,
      TenderType: this.checkDetails?.PaymentsTendered[0].TenderTypeID,
      ShowSignatureSegment: false,
      TotalPaid: this.checkDetails?.PaymentsTendered[0].Amount,
      ContactsInfoList: this.setContactsInfoList(recipientModel.Emails)
    };
    this.manageLoadingService.startLoading();
    this.subscription.add(this.sendNotificationService.Post(sendNotificationRequest)
      .pipe(finalize(() => { this.manageLoadingService.finishLoading(); }))
      .subscribe({
        next: () =>
        {
          this.snackBarService.showSuccess(this.translate.instant('PAYMENT_PROCESSOR.PROCESS.SEND_NOTIFICATIONS_SUCCESSFULLY'), '', null, 'left');
        },
        error: (error) =>
        {
          if (error.error?.title)
          {
            this.snackBarService.showError(error.error?.title, '', null, 'left');
          }
        }
      }));
  }
  setContactsInfoList(emails: string[]): ContactInfo[]
  {
    const contactsInfoList = [];
    if (this.checkDetails?.Contact && emails.includes(this.checkDetails.Contact.Email))
    {
      const displayName = this.checkDetails.Contact.DisplayName.split(' ');
      contactsInfoList.push({
        Email: this.checkDetails.Contact.Email,
        PositionNo: 1,
        FirstName: displayName[0],
        LastName: displayName[1],
      });
      emails.forEach((email, index) =>
      {
        if (email !== this.checkDetails.Contact.Email)
        {
          contactsInfoList.push({
            Email: email,
            PositionNo: index + 1,
          });
        }
      });
    } else
    {
      emails.forEach((email, index) =>
      {
        contactsInfoList.push({
          Email: email,
          PositionNo: index + 1,
        });
      });
    }
    return contactsInfoList;
  }

  ngAfterViewInit(): void
  {
    this.subscription.add(this.checkTabItemContainer?.changes?.subscribe((elements: QueryList<ElementRef>) =>
    {
      this.checkTabItemContainerHTMLElement = elements;
    }));

  }

  updatePaymentMethodsTitle(isReturn: boolean): void
  {
    this.paymentMethodTitle = !isReturn ? this.translate.instant('PAYMENT_PROCESSOR.PROCESS.PAYMENT_METHODS')
      : this.translate.instant('PAYMENT_PROCESSOR.PROCESS.RETURN_PAYMENT_METHODS');
  }

  updateTooltip(): void
  {
    setTimeout(() =>
    {
      this.noteElements.forEach((elRef, index) =>
      {
        const el = elRef.nativeElement;
        this.isOverflowing[index] = el.scrollWidth > el.clientWidth;
      });
    });
  }
  absAmount(amount: number): number
  {
    return Math.abs(amount);
  }

  ngOnDestroy()
  {
    this.subscription?.unsubscribe();
    this.responsiveServiceSubscription?.unsubscribe();
    this.dialog?.closeAll();
  }
}
