import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output, SkipSelf, ViewChild } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders, ResponsiveService } from '@tei/core';
import { QuickSearchComponent } from '@tei/material';
import { Account, GetAccountsResponse } from '../../../@intermediate/models/get-account.model';

@Component({
  selector: 'tei-common-payment-processor-account-quick-search',
  templateUrl: './account-quick-search.component.html',
  styleUrls: ['./account-quick-search.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AccountQuickSearchComponent)],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AccountQuickSearchComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit
{
  form: UntypedFormGroup;
  model: GetAccountsResponse;
  quickSearchValue = {};
  accountItemArray$: Account[];

  @ViewChild('accountItemQuickSearch') private accountItemQuickSearch: QuickSearchComponent<any>;

  @Input() selectedAccountItem: Account;
  @Input() accountList = Array<Account>();
  @Input() ContactID: string;

  @Output() accountItemClickEvent = new EventEmitter<any>();
  @Output() removeAccountItemClickEvent = new EventEmitter<Account>();
  @Output() selectedAccountItemEvent = new EventEmitter<Account>();

  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder)
  {
    super(errorStateMatcher);
    super.writeValue(this.selectedAccountItem);
  }

  valueConversion(field: string): void
  {
    const control = this.form.get(field);
    if (control.invalid)
    {
      this.onAccountItemChange(null);
    }
  }

  ngOnInit(): void
  {
    this.form = this.formBuilder.group({
      quickSearchValue: [null],
      accountName: [this.getTitle(this.selectedAccountItem), [Validators.required]]
    });

    setTimeout(() =>
    {
      this.formControls.accountName.markAsTouched();
    }, 0);
  }

  getTitle(item: Account): string
  {
    if (!item) { return ''; }
    const AccountName = item.AccountName.trim();
    const MemberID = item.MemberID.trim();
    if (!AccountName || AccountName === '')
    {
      return MemberID;
    }

    if (!MemberID || MemberID === '')
    {
      return AccountName;
    }

    return AccountName + ' - ' + MemberID;
  }

  loadAccount(accounts: Array<Account>): void
  {
    if (this.accountItemQuickSearch)
    {
      this.accountList = accounts;
    }

    if (this.accountList.length === 1)
    {
      this.onAccountItemChange(this.accountList[0]);
    }
    else if (this.accountList.length > 1)
    {
      let defaultAccount = this.accountList.filter(a => a.isDefault && a.AllowAR);

      if (!defaultAccount.length)
      {
        defaultAccount = this.accountList.filter(a => a.AllowAR);
      }
      if (defaultAccount.length)
      {
        this.onAccountItemChange(defaultAccount[0]);
      }
    }

    if (!this.selectedAccountItem)
    {
      this.openQuickSearch();
    }
  }

  get formControls()
  {
    return this.form.controls;
  }


  onAccountItemChange(item: Account): void
  {
    if (item && !item.AllowAR) { return; }
    this.onChange(item);
    this.accountItemClickEvent.emit(item);
    this.passSelectedAccountItem(item);

    if (item)
    {
      this.formControls.accountName.setValue(this.getTitle(this.selectedAccountItem));
      this.closeQuickSearch();
    } else
    {
      this.formControls.accountName.setValue(null);
    }
  }

  onAccountItemRemove(event: Account): void
  {
    this.removeAccountItemClickEvent.emit(event);
    this.onChange(null);
  }

  private passSelectedAccountItem(item: Account)
  {
    this.selectedAccountItem = item;
    this.selectedAccountItemEvent.emit(item);
  }

  openQuickSearch(): void
  {
    this.accountItemQuickSearch.openQuickSearch();
  }

  closeQuickSearch(): void
  {
    this.accountItemQuickSearch.closeQuickSearch();
  }
}
