import { Component, OnInit } from '@angular/core';
import { CategoryEnum, EventTypeEnum, GenericEventModel, GenericEventService, RouteService } from '@tei/core';

@Component({
  selector: 'tei-common-payment-processor',
  templateUrl: './payment-processor.component.html',
  styleUrls: ['./payment-processor.component.scss']
})
export class PaymentProcessorComponent implements OnInit
{
  constructor(
    private readonly routeService: RouteService, private genericEventService: GenericEventService
  )
  {
    this.genericEventService.onChangeGenericEvent({
      module: { Category: CategoryEnum.Pos, Name: 'common-payment-processor' },
      eventType: EventTypeEnum.onChangeSideBarVisibility,
      data: { sideBarVisible: false, headerTitle: 'Payment' }
    } as GenericEventModel);

    this.routeService.ngOnInit();
  }

  ngOnInit(): void
  {
    this.setScrollableContainerHight()
  }

  setScrollableContainerHight():void {
    const windowInnerHeight = window.innerHeight;

    const scrollableContainer = document.querySelector<HTMLElement>('.scrollable-container')!;
    if (scrollableContainer)
    {
      scrollableContainer.style['height'] = `${ windowInnerHeight }px`;
    }
  }
}
