import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { SelectedData } from '../../@intermediate/models/get-gift-card.model';
import { PaymentsTendered, PaymentsTenderedStatus, Printer, TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { CheckDetails } from './../../@intermediate/models/process.model';

@Component({
  selector: 'tei-common-payment-processor-gift-card-pay',
  templateUrl: './gift-card-pay.component.html',
  styleUrls: ['./gift-card-pay.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(GiftCardPayComponent)]
})
export class GiftCardPayComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  showGratuity: boolean;
  totalApproved: number;
  printerList: Array<Printer> = [];
  enableTipsOnGiftCard = false;
  checkDetails: CheckDetails;
  selectedData: SelectedData;
  get approvedPaymentsTendered(): PaymentsTendered
  {
    return this.checkDetails?.PaymentsTendered.filter(p =>
      p.TenderTypeID === TenderTypeID.GiftCertificate
      && p.Status === PaymentsTenderedStatus.Approved)[0];
  }

  constructor(@SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject(MAT_DIALOG_DATA) public giftCardData: any,
    public manageLoadingService: ManageLoadingService,
    private dialogRef: MatDialogRef<GiftCardPayComponent>,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.loadDataFromDialogInput();
  }

  loadDataFromDialogInput(): void
  {
    if (!this.giftCardData)
    {
      return;
    }

    this.showGratuity = this.giftCardData.showGratuity;
    this.totalApproved = this.giftCardData.total;
    this.printerList = this.giftCardData.printerList;
    this.enableTipsOnGiftCard = this.giftCardData.enableTipsOnGiftCard;
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(checkDetail =>
    {
      this.checkDetails = checkDetail;
    }));
  }

  close(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
