import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from "@tei/core";
import { SnackBarService } from '@tei/material';
import { OnNotePost } from '@tei/material/lib/notes/models/on-note-post.model';
import { firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { NoteDetailType, NoteType, PaymentDetailsType } from '../../@intermediate/models/payment-process/get-process.model';
import { GetNoteRequest } from '../../@intermediate/models/Note/get-note.model';
import { PostNoteRequest, PostNoteDetail } from '../../@intermediate/models/Note/post-note.model';
import { NoteService } from '../../@services/note.service';

@Component({
  selector: 'tei-pos-food-and-beverage-note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.scss']
})
export class NoteComponent implements OnInit
{
  maxLengthNote = 255;
  label : string;
  type: NoteType;
  itemID: string;
  value: string;
  printOnReceipt?: boolean = null;
  IsEditable = true;
  IsAdmin = true;
  showDialog = false;
  enableReceiptCheckComments: boolean;
  constructor(
    @Inject('INoteService') private service: NoteService,
    @Inject(MAT_DIALOG_DATA) public noteData: GetNoteRequest,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    public noteComponentRef: MatDialogRef<NoteComponent>
  ) { }

  ngOnInit(): void
  {
    if (this.noteData)
    {
      this.type = this.noteData.Note.Type;
      this.label = this.noteData.Note.Type == NoteType.RetailTrxNote ? "Transaction Notes" : this.noteData.Note.Label;
      this.itemID = this.noteData.ItemID;
      this.value = this.noteData.Note.Value;
      this.enableReceiptCheckComments = this.noteData.Note.NoteDetails?.some(
                                        p => p.NoteDetailType === NoteDetailType.FoodAndBeverageShowOnReceipt
                                        ) ?? false;

      this.printOnReceipt = this.noteData.Note.NoteDetails?.filter(
                            p => p.NoteDetailType === NoteDetailType.FoodAndBeverageShowOnReceipt
                            )[0]?.Value;

      this.showDialog = true;
      
      if(this.type == NoteType.RetailReceiptNote || this.type == NoteType.RetailTrxNote)
      {
        this.maxLengthNote = 200;
      }
    }
  }

  async onPost(itemText: string | OnNotePost)
  {
    this.value = typeof itemText === 'string' ? itemText : itemText.Note;
    let request = new PostNoteRequest();
    request.PaymentDetailsTypes = PaymentDetailsType.Note;
    request.PaymentDetails = new PostNoteDetail();
    request.PaymentDetails.Value = this.value;
    request.PaymentDetails.Type = this.type;
    request.PaymentDetails.ItemID = this.itemID;
    request.PaymentDetails.PrintOnReceipt = typeof itemText === 'string' ? null : itemText.PrintOnReceipt;

    this.manageLoadingService.startLoading();

    try
    {
      const response = await firstValueFrom(
        this.service.PostNote(request).pipe(finalize(() => this.manageLoadingService.finishLoading()))
      );

      if (response)
      {
        this.noteData.Note.Value = this.value;
        const noteDetail = this.noteData.Note.NoteDetails?.filter(
                            p => p.NoteDetailType === NoteDetailType.FoodAndBeverageShowOnReceipt
                            )[0];
        if(noteDetail)
        {
          noteDetail.Value = request.PaymentDetails.PrintOnReceipt;
        }
        this.snackBarService.showSuccess(this.translate.instant('PAYMENT_PROCESSOR.NOTE.POST_NOTE_SUCCESS'), '', null, 'left');
        this.noteComponentRef.close({ isChanged: true, newNote: this.noteData.Note });
      }
      else
      {
        this.snackBarService.showSuccess(this.translate.instant('PAYMENT_PROCESSOR.NOTE.POST_NOTE_ERROR'), '', null, 'left');
      }
    } catch (error)
    {
    }
  }


  onCancel(): void
  {
    this.noteComponentRef.close({ isChanged: false });
  }
}
