import { Inject, Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { IAuthService, IAuthToken } from '@tei/abstraction';
import { AppIdentifierService, PostHearBeatService } from '@tei/core';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

@Injectable()
export class PaymentAuthGuard implements CanActivate
{
  constructor(@Inject('IAuthService') private authService: IAuthService,
    private appIdentifierService: AppIdentifierService,
    public hearBeatService: PostHearBeatService,
    private router: Router)
  {
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot)
    : Observable<boolean> | Promise<boolean> | boolean
  {
    return new Promise((resolve, reject) =>
    {
      this.authService.getToken().subscribe((token: IAuthToken) =>
      {
        const accessToken = token.getPayload().access_token;
        if (!accessToken)
        {
          this.redirectMainLoginPage(resolve);
        } else
        {
          this.hearBeatService.postHeartbeat().subscribe({
            next: () =>
            {
              this.checkExtendedToken(token, resolve);
            },
            error: () =>
            {
              this.redirectMainLoginPage(resolve);
            }
          });
        }
      });
    });
  }

  private checkExtendedToken(token: IAuthToken, resolve: (value: boolean | PromiseLike<boolean>) => void)
  {
    const extendedAccessToken = token.getPayload().extended_access_token;
    const appSession = this.appIdentifierService.getAppSession();
    if (!extendedAccessToken || !appSession)
    {
      if (!appSession)
      {
        location.replace(window.location.origin);
      }
      else
      {
        this.router.navigate([appSession.baseAddress + "/logout"]);
      }
      resolve(false);
    }

    else
    {
      this.hearBeatService.postHeartbeatExtended().subscribe({
        next: () =>
        {
          resolve(true);
        },
        error: () =>
        {
          this.router.navigate([appSession.baseAddress + "/logout"]);
          resolve(false);
        }
      });
    }
  }

  private redirectMainLoginPage(resolve: (value: boolean | PromiseLike<boolean>) => void)
  {
    this.appIdentifierService.removeAppSession();
    location.replace(window.location.origin);
    resolve(false);
  }
}
