import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostPaymentPrintRequest } from '../@intermediate/models/Print/post-payment-print-request.model';
import { PostPaymentPrintResponse } from '../@intermediate/models/Print/post-payment-print-response.model';
import { IPrintService } from '../@intermediate/print.interface';

@Injectable()
export class PrintService implements IPrintService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/Print';
  }

  public Post(request: PostPaymentPrintRequest): Observable<PostPaymentPrintResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPaymentPrintResponse>(apiUrl, request);
  }
}
