import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICheckContactService } from '../@intermediate/check-contact.interface';
import { AddContactToCheckRequest, AddContactToCheckResponse } from '../@intermediate/models/add-contact-to-check.model';
import { DeleteContactFromCheckResponse } from '../@intermediate/models/delete-contact-from-check.model';

@Injectable()
export class CheckContactService implements ICheckContactService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentProcessor/Process';
  }


  public AddContactToCheck(request: AddContactToCheckRequest): Observable<AddContactToCheckResponse>
  {
    const apiUrl = this.serviceUrl + '/SetOrderContact';
    return this.http.post<AddContactToCheckResponse>(apiUrl, request);
  }

  public DeleteContactFromCheck(request: AddContactToCheckRequest): Observable<DeleteContactFromCheckResponse>
  {
    const apiUrl = this.serviceUrl + '/SetOrderContact';
    return this.http.post<DeleteContactFromCheckResponse>(apiUrl, request);
  }

}
