import { Component, Input, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { TeiCurrencyTranslateService } from "@tei/core-translate";
import { CreditDebitDialog } from '../../../@intermediate/models/credit-debit-model';
import { PaymentProcessConfiguration } from '../../../@intermediate/models/payment-configuration.model';
import { Options } from '../../../@intermediate/models/payment-process/get-process.model';

@Component({
  selector: 'tei-common-payment-processor-non-integrated',
  templateUrl: './non-integrated.component.html',
  styleUrls: ['./non-integrated.component.scss'],
})
export class NonIntegratedComponent implements OnInit
{
  @Input() creditDebitData: CreditDebitDialog;
  @Input() form: UntypedFormGroup;
  @Input() configuration: PaymentProcessConfiguration;
  currencySymbol: string;
  typeList: Options[] = [];

  constructor(private currencyService: TeiCurrencyTranslateService)
  {
  }

  get formControls()
  {
    return this.form.controls;
  }

  onTotalApprovedBlur(): void
  {
    const remainingTotal = this.returnValidValue(this.formControls?.totalApproved?.value);
    let subtotalPaid = this.returnValidValue(this.formControls?.subtotalPaid?.value);
    if (subtotalPaid > remainingTotal)
    {
      subtotalPaid = remainingTotal;
    }
    this.formControls?.subtotalPaid?.setValue(subtotalPaid.toFixed(2));
    this.onSubTotalPaidAndGratuityInput();
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    const PaymentMethodsList = this.creditDebitData.tenderType.PaymentMethods.find(item => item.PaymentMethodID === 2);

    this.typeList = PaymentMethodsList.Options;
  }

  valueConversion(field: string): void
  {
    const control = this.form.get(field);
    control.patchValue(Number(control.value).toFixed(2));

  }

  onSubtotalPaidInput(): void
  {
    const subtotalPaid = this.returnValidValue(this.formControls?.subtotalPaid?.value);
    const gratuityValue = this.returnValidValue(this.formControls?.gratuityAmount?.value);
    this.formControls?.totalPaid?.setValue((subtotalPaid + gratuityValue).toFixed(2));
    this.onSubTotalPaidAndGratuityInput();
  }


  onGratuityAmountInput(): void
  {
    const subtotalPaid = this.returnValidValue(this.formControls?.subtotalPaid?.value);
    const gratuityAmount = this.returnValidValue(this.formControls?.gratuityAmount?.value);
    const percent = subtotalPaid ? (gratuityAmount / subtotalPaid * 100).toFixed(2) : '0.00';
    this.formControls?.gratuityPercent?.setValue(percent);
    this.formControls?.totalPaid?.setValue((subtotalPaid + gratuityAmount).toFixed(2));
  }

  onSubTotalPaidAndGratuityInput(): void
  {
    const subtotalPaid = this.returnValidValue(this.formControls?.subtotalPaid?.value);
    const gratuityPercent = this.returnValidValue(this.formControls?.gratuityPercent?.value);
    const gratuityValue = Number((subtotalPaid * gratuityPercent / 100).toFixed(2));
    this.formControls?.gratuityAmount?.setValue(gratuityValue.toFixed(2));
    this.formControls?.totalPaid?.setValue((subtotalPaid + gratuityValue).toFixed(2));
  }

  returnValidValue(value: string): number
  {
    if (!value)
    {
      value = '0.00';
    }

    return Number(value.toString().replace(/[%]/g, ''));
  }
}
