import { Component, Inject, Input, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { Subscription } from 'rxjs';
import { IPaymentProcessGenericService } from '../../../@intermediate/IPaymentProcessGenericService.interface';
import { Account } from '../../../@intermediate/models/get-account.model';
import { PaymentsTenderedStatus, TenderTypeID } from '../../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest, PaymentResponse } from '../../../@intermediate/models/payment-process/post-payment.model';
import { PaymentVoidRequest } from '../../../@intermediate/models/payment-void/post-payment-void.model';
import { CheckDetails } from '../../../@intermediate/models/process.model';


@Component({
  selector: 'tei-common-payment-processor-on-account-commit-payment',
  templateUrl: './on-account-commit-payment.component.html',
  styleUrls: ['./on-account-commit-payment.component.scss']
})
export class OnAccountCommitPaymentComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  @Input() form: UntypedFormGroup;
  @Input() stepper: MatStepper;
  @Input() showGratuity = false;
  @Input() selectedAccount: Account;
  @Input() tenderTypeID: TenderTypeID;
  @Input() checkDetails: CheckDetails;
  @Input() result: PaymentResponse;

  subscription: Subscription = new Subscription();
  currencySymbol: string;

  get accountTitle() { return !this.selectedAccount ? '' : (this.selectedAccount?.AccountName + ' - ' + this.selectedAccount.MemberID); }

  constructor(
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private DialogRef: MatDialogRef<OnAccountCommitPaymentComponent>,
    private currencyService: TeiCurrencyTranslateService,
    public manageLoadingService: ManageLoadingService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.valueConversion('totalApproved');
    this.valueConversion('paymentTotal');
  }

  onlyNumber(event)
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  get formControls()
  {
    return this.form.controls;
  }

  valueConversion(field: string): void
  {
    const control = this.form?.get(field);
    control?.patchValue(Number(control.value).toFixed(2));
    control?.setErrors(null);
  }

  onGratuityValueInput(): void
  {
    const totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    const gratuityValue = this.returnValidValue(this.formControls?.gratuityValue?.value);
    const percent = totalApproved ? (gratuityValue / totalApproved * 100).toFixed(2) : '0.00';
    this.formControls?.gratuityPercent?.setValue(percent);
    this.formControls?.paymentTotal?.setValue((totalApproved + gratuityValue).toFixed(2));
  }

  onTotalApprovedAndGratuityInput(): void
  {
    const totalApproved = this.returnValidValue(this.formControls?.totalApproved?.value);
    const gratuityPercent = this.returnValidValue(this.formControls?.gratuityPercent?.value);
    const gratuityValue = Number((totalApproved * gratuityPercent / 100).toFixed(2));
    this.formControls?.gratuityValue?.setValue(gratuityValue.toFixed(2));
    this.formControls?.paymentTotal?.setValue((totalApproved + gratuityValue).toFixed(2));
  }

  returnValidValue(value: string): number
  {
    if (!value)
    {
      value = '0.00';
    }

    return Number(value.replace(/[%]/g, ''));
  }

  commitPayment(): void
  {
    const request = new PaymentRequest();
    request.PaymentItemID = this.checkDetails.CheckUID;
    request.PaymentMethodID = this.tenderTypeID;
    const paymentsTendered = this.getPaymentTendered();
    if (paymentsTendered)
    {
      request.ContactID = paymentsTendered?.ContactID;
      request.AccountID = paymentsTendered?.AccountID;
      request.ApprovedTransactionID = paymentsTendered?.TrxUID;
    } else
    {
      request.ContactID = this.checkDetails?.Contact?.ContactID;
      request.AccountID = this.selectedAccount.AccountID;
      request.ApprovedTransactionID = this.result.TrxID;
    }

    request.Amount = this.returnValidValue(this.formControls.paymentTotal.value);
    request.TipAmount = this.returnValidValue(this.formControls?.gratuityValue?.value);
    request.IsApproved = true;
    request.OrderID = this.checkDetails.OrderSeqID;
    this.paymentProcessGenericService.ProcessPayment(request, this.DialogRef, false).subscribe();
  }

  getPaymentTendered()
  {
    return this.checkDetails?.PaymentsTendered?.find(p => p.TenderTypeID === TenderTypeID.MemberAccount && p.Status !== PaymentsTenderedStatus.Voided);
  }

  close(): void
  {
    this.DialogRef.close(this.result);
  }

  voidPayment()
  {

    const request = new PaymentVoidRequest();
    const paymentsTendered = this.getPaymentTendered();

    if (paymentsTendered)
    {
      request.TrxIDs = [paymentsTendered.TrxUID];
    }
    else
    {
      request.TrxIDs = [this.result.TrxID];
    }
    request.PaymentItemID = this.checkDetails?.CheckUID;
    request.PaymentInitiatorUID = this.checkDetails.PaymentRecordUID;
    this.paymentProcessGenericService.voidPayments(request).subscribe(result =>
    {
      if (result)
      {
        this.DialogRef.close(Object.assign({}, new PaymentResponse()));
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
