import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, SkipSelf } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { PrintConfiguration } from '../../@intermediate/models/payment-configuration.model';
import { Contact, Printer, PrinterType } from '../../@intermediate/models/payment-process/get-process.model';
import { CounterEvents, ReceiptModel, ReceiptType } from '../../@intermediate/models/process.model';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { Subscription } from 'rxjs';


@Component({
  selector: 'tei-common-payment-processor-receipt',
  templateUrl: './receipt.component.html',
  styleUrls: ['./receipt.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(ReceiptComponent)]
})
export class ReceiptComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnChanges, OnDestroy
{
  @Input() paymentCompleted = false;
  @Input() remaining = 0;
  @Input() printerList: Printer[] = [];
  @Input() printConfigurations: PrintConfiguration[] = [];
  @Input() contact: Contact;
  @Output() receiptData = new EventEmitter<ReceiptModel>();
  @Output() receiptActionEvent = new EventEmitter<ReceiptModel>();

  subscription: Subscription = new Subscription();
  emails: Array<string> = [];
  defaultValueList: Array<string> = [];
  receiptType = ReceiptType;
  copyCount = 1;
  form: UntypedFormGroup;

  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  selectable = true;
  removable = true;
  counterEvents = CounterEvents;
  disabledPayButton = true;
  constructor(private formBuilder: UntypedFormBuilder,
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    public manageLoadingService: ManageLoadingService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    const defaultPrinter = this.printerList.filter(printer => printer.Type === PrinterType.Primary);

    this.createForm(defaultPrinter);

    this.handlePrinterIDChange();
  }

  handlePrinterIDChange(): void
  {

    this.paymentProcessGenericService.selectedPrinterID = Number(this.form.get('PrinterID')?.value);

    this.subscription.add(this.form.get('PrinterID').valueChanges.subscribe(printerID => this.paymentProcessGenericService.selectedPrinterID = Number(printerID)));
  }

  private createForm(defaultPrinter: Printer[])
  {
    this.form = this.formBuilder.group({
      Type: [ReceiptType.Print],
      CopyCount: [this.copyCount],
      PrinterID: [defaultPrinter.length > 0 ? defaultPrinter[0].PrinterID : null],
      PrintConfigurations: this.formBuilder.array(this.printConfigurations.map(item =>
      {
        return this.formBuilder.group(
          {
            Name: [item.Name],
            Title: [item.Title],
            checked: [false]
          }
        );
      })),
    });
  }

  addEmailChip(emails: Array<string>): void
  {
    this.emails = emails;
  }

  getPrinterName(printerID: string): string
  {
    return this.printerList?.filter(item => item.PrinterID === Number(printerID))[0]?.Name;
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (!changes)
    {
      return;
    }

    if (changes?.contact && this.contact?.Email)
    {
      this.defaultValueList = [this.contact?.Email];
    }

    if (this.printerList)
    {
      this.formControls?.PrinterID.setValue(this.printerList[0]?.PrinterID);
    }
  }

  get formControls()
  {
    return this.form?.controls;
  }

  get printConfigurationsControl()
  {
    return this.form.controls.PrintConfigurations as UntypedFormArray;
  }

  changeReceiptData(): void
  {
    this.disabledPayButton = this.emails.length === 0;
    this.receiptData.emit(this.form.value);
  }

  changeCopyCount(event: number): void
  {
    let count = Number(this.formControls.CopyCount?.value);
    if (event === this.counterEvents.Minus)
    {
      if (count > 1)
      {
        count--;
        this.formControls.CopyCount.setValue(count);
      } else if (count === 1)
      {
        this.formControls.CopyCount.setValue(1);
      }
    }
    if (event === this.counterEvents.Plus)
    {
      count++;
      this.formControls.CopyCount.setValue(count);
    }
  }

  receiptAction(): void
  {
    const receiptModel: ReceiptModel = Object.assign(new ReceiptModel(), this.form.value);
    if (receiptModel.Type === ReceiptType.Email)
    {
      receiptModel.Emails = this.emails;
    }
    receiptModel.PrintConfigurations = receiptModel.PrintConfigurations.reduce((accumulator, currentValue: PrintConfiguration & { checked: boolean; }) =>
    {
      if (currentValue.checked)
      {
        accumulator.push({
          Name: currentValue.Name,
          Title: currentValue.Title,
        });
      }
      return accumulator;
    }, []);
    this.receiptActionEvent.emit(receiptModel);
  }

  setPayButtonDisabled(event: boolean): void
  {
    this.disabledPayButton = event;
  }

  ngOnDestroy()
  {
    this.subscription?.unsubscribe();
  }
}
