import { Component, Input, OnInit } from '@angular/core';
import { Contact, PaymentItem, PaymentStatus } from '../../@intermediate/models/payment-process/get-process.model';

@Component({
  selector: 'tei-common-payment-processor-check-card',
  templateUrl: './check-card.component.html',
  styleUrls: ['./check-card.component.scss']
})
export class CheckCardComponent implements OnInit
{

  @Input() checkDetail: PaymentItem;
  @Input() checkNumber: number;
  @Input() paymentStatus: PaymentStatus;

  PaymentStatus = PaymentStatus;
  status: string;

  ngOnInit(): void
  {
    switch (this.paymentStatus)
    {
      case this.PaymentStatus.Paid:
        this.status = 'Paid';
        break;
      case this.PaymentStatus.PartiallyPaid:
        this.status = 'Remaining';
        break;
      case this.PaymentStatus.Unpaid:
        this.status = 'Remaining';
        break;

      default:
        break;
    }
  }

  getTooltipInfo(contact: Contact)
  {
    let output = '';
    if (contact?.DisplayName && contact?.MemberID)
    {
      output = contact.DisplayName + ' - ' + contact.MemberID;
    }
    else
    {
      output = contact?.DisplayName || contact?.MemberID;
    }

    return output;
  }

}
