import { PaymentInitiator } from "./initialize.model";
import { PaymentButtonsStatus } from "./process.model";

export class PaymentProcessConfiguration
{
  PaymentInitiator: PaymentInitiator;
  PaymentButtonsStatus: PaymentButtonsStatusConfig;
  SuccessUrl: string;
  FailedUrl: string;
  CancelUrl: string;
  RefId: string;
  NonIntegratedCreditTenderTypeModel: NonIntegratedCreditTenderTypeModel;
  OnAccountTenderTypeModel: OnAccountTenderTypeModel;
  NeedFinalizeOrder: boolean;
  PrintConfigurations: Array<PrintConfiguration>;
  GiftCardTenderTypeModel: GiftCardTenderTypeModel;
  CashEQTenderTypeModel: CashEQTenderTypeModel;
  PrintReceiptOptions: Array<PrintReceiptOption>;
  AllowChangeContactLookup: boolean;
}

export enum PrintReceiptOption
{
  OrderReceipt,
  SlipReceipt,
  GiftCardReceipt,
  PackageReceipt
}

export class CashEQTenderTypeModel
{
  AllowTip: boolean;
}

export class GiftCardTenderTypeModel
{
  AllowPrint = true;
  AllowApprove = true;
}

export class PaymentButtonsStatusConfig
{
  Paid: PaymentButtonsStatus;
  PartiallyPaid: PaymentButtonsStatus;
  Unpaid: PaymentButtonsStatus;
}

export class NonIntegratedCreditTenderTypeModel
{
  AllowSubtotalPaid: boolean;
  AllowGratuity: boolean;
}

export class OnAccountTenderTypeModel
{
  AllowPrint: boolean;
}

export class PrintConfiguration
{
  Name: string;
  Title: string;
}
