import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IPaymentProcessGenericService } from '../../@intermediate/IPaymentProcessGenericService.interface';
import { PointPayDialogData } from '../../@intermediate/models/get-redeemable-points.model';
import { Printer, TenderTypeID } from '../../@intermediate/models/payment-process/get-process.model';
import { PaymentRequest } from '../../@intermediate/models/payment-process/post-payment.model';
import { CheckDetails } from './../../@intermediate/models/process.model';

@Component({
  selector: 'tei-common-payment-processor-point-pay-dialog',
  templateUrl: './point-pay-dialog.component.html',
  styleUrls: ['./point-pay-dialog.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(PointPayDialogComponent)]
})
export class PointPayDialogComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  showGratuity: boolean;
  totalApproved: number;
  printerList: Array<Printer> = [];
  checkDetails: CheckDetails;
  currencySymbol: string;


  constructor(@SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder,
    public manageLoadingService: ManageLoadingService,
    @Inject(MAT_DIALOG_DATA) public cashPayDialogData: PointPayDialogData,
    private dialogRef: MatDialogRef<PointPayDialogComponent>,
    private currencyService: TeiCurrencyTranslateService,
    @Inject('IPaymentProcessGenericService') public paymentProcessGenericService: IPaymentProcessGenericService)
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.currencySymbol = this.currencyService.getCurrencySymbol();
    this.subscription.add(this.paymentProcessGenericService.changeCheckDetailsSubject.subscribe(checkDetails =>
    {
      this.checkDetails = checkDetails;
    }));

    const isNegativeTotal = this.cashPayDialogData.total < 0;
    const minValue = isNegativeTotal ? -Math.abs(this.lowestAmount()) : 1;
    this.form = this.formBuilder.group({
      paymentAmount: [this.lowestAmount(), [
        Validators.required,
        Validators.pattern(/^-?[0-9]*\.?[0-9]*$/), // Allow negative values
        Validators.min(minValue),
        Validators.max(this.lowestAmount())
      ]],
      paymentAmountValue: [this.lowestAmountValue(), [
        Validators.required,
        Validators.min(isNegativeTotal ? -Math.abs(this.lowestAmountValue()) : 0.01),
        Validators.max(isNegativeTotal ? -0.01 :this.lowestAmountValue())
      ]]
    });
    this.subscription.add(this.form.get('paymentAmount').valueChanges.subscribe(value => {
      this.dividedBy100('paymentAmountValue', value);
      if (value > this.lowestAmount() && this.cashPayDialogData?.total > 0) {
        this.form.get('paymentAmount').setValue(this.lowestAmount(), { emitEvent: false });
      }
    }));
  }

  lowestAmount(): number
  {

    let amount = this.cashPayDialogData?.contactPoints?.RedeemablePoints >= this.cashPayDialogData?.contactPoints?.AvailablePoints ?
      this.cashPayDialogData?.contactPoints?.AvailablePoints : this.cashPayDialogData?.contactPoints?.RedeemablePoints;
    return parseFloat((this.cashPayDialogData?.total * 100 < amount ? this.cashPayDialogData?.total * 100 : amount)?.toFixed(2));
  }

  lowestAmountValue(): number
  {
    let amount = this.cashPayDialogData?.contactPoints?.RedeemablePointsValue >= this.cashPayDialogData?.contactPoints?.AvailablePointsValue ?
      this.cashPayDialogData?.contactPoints?.AvailablePointsValue : this.cashPayDialogData?.contactPoints?.RedeemablePointsValue;
    return parseFloat((this.cashPayDialogData?.total < amount ? this.cashPayDialogData?.total : amount).toFixed(2));
  }


  close(): void
  {
    this.dialogRef.close();
  }

  dividedBy100(field, fromValue)
  {
    const control = this.form.get(field);
    control.patchValue(Number(fromValue / 100).toFixed(2));
  }

  passValuesOnClose(): void
  {
    this.dialogRef.close({
      TenderTypeID: TenderTypeID.PointsRedemption,
      Cash: this.form.controls.paymentAmountValue.value
    });
  }

  processPayment(): void
  {
    this.manageLoadingService.startLoading();
    const request = new PaymentRequest();
    request.RedeemedPoints = 0.0;
    request.PaymentMethodID = TenderTypeID.PointsRedemption;

    request.RedeemedPoints = parseFloat((this.form.controls.paymentAmountValue.value * 100).toFixed(2));

    request.Amount = this.form.controls.paymentAmountValue.value;
    request.OrderID = this.checkDetails.OrderSeqID;
    request.CashBack = 0.0;
    request.PaymentItemID = this.checkDetails.CheckUID;
    this.subscription.add(this.paymentProcessGenericService.ProcessPayment(request, this.dialogRef, false)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe());
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
